
// about
.about {
    padding: 150px 0;
    background: var(--fon-blokov);
    overflow: hidden;

    &__gallery {
        margin-bottom: 60px;
        &_swiper {
            overflow: visible;
        }
    }

    &__row {
        display: flex;
    }
    &__row .container {
        display: flex;
    }

    &__content {
        flex: 0 0 100%;
        display: flex;
    }

    &__left {
        flex: 1 0 40%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        max-width: 50%;
        padding-right: 60px;

        &_text {
            font-style: italic;
            & p:not(:last-child) {
                margin-bottom: 16px;
            }
            & span {
                display: block;
                font-weight: 400;
                font-size: 20px;
                line-height: 110%;
                letter-spacing: -0.02em;
                color: var(--akcentnyy-zelenyy);
                margin-top: 10px;
            }
        }
    }

    &__right {
        flex: 0 0 50%;
        max-width: 50%;
        transform: translate(-100%, 0);
        
        &_top {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            padding-top: 16px;
            border-top: 1px solid #e6e6eb;
            margin-bottom: 64px;

            max-width: 880px;
            padding-right: 10px;
        }

        &_icon {
            display: block;
            min-width: 20px;
            width: 20px;
            height: 20px;
        }

        &_text {
            max-width: 666px;
            font-style: italic;
            line-height: 105%;
            & span {
                color: var(--shrift-temnyy);
            }
        }

        & p {
            font-style: italic;
            line-height: 105%;
            & span {
                color: var(--shrift-temnyy);
            }
        }
    }

    &__advantages {
        &_title {
            font-weight: 500;
            font-size: 32px;
            margin-bottom: 32px;
        }

        &_swiper {
            margin: 0;
        }

        &_swiper-slide {
            border-radius: 5px;
            padding: 20px;
            box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
            background: var(--fon);
            min-height: 360px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
        }
    }
}

.about-page {
    & .about__advantages {
        &_swiper-slide {
            box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
            background: var(--fon-blokov);
        }
    }
}


// media about
@media (max-width: 1100px) {
    .about {
        display: flex;
        flex-direction: column;
        gap: 30px;
        padding: 100px 10px;

        &__row, &__content,  &__left, & .container {
            display: contents;
        }

        &__title {
            margin-bottom: 20px;
        }

        &__gallery {
            order: 1;
            margin-bottom: 20px;
        }
    
        &__right {
            order: 2;
            flex: 0 0 100%;
            max-width: 100%;
            transform: translate(0, 0);
            &_top {
                max-width: none;
                padding-right: 0;
            }
            &_text {
                max-width: 80%;
            }
        }
    
        &__advantages {
            &_swiper {
                overflow: visible;
            }
            &_swiper-slide {
                min-height: 300px;
            }
        }

        &__left_text {
            order: 3;
        }
    }
    

    .about-page {
        display: flex;
        flex-direction: column;
        gap: 30px;
        padding: 0 10px;
        & .container {
            display: contents;
        }
        & .about__gallery {
            order: 4;
            margin-bottom: 0;
        }
        & .about__right {
            &_top {
                margin: 0;
                padding: 0;
                border: none;
            }
        }
    }
}

@media (max-width: 768px) {
    .about {
        gap: 24px;
        padding: 70px 10px;

        &__title, &__gallery {
            margin-bottom: 6px;
        }

        &__gallery {
            &_image img {
                min-height: 240px;
                max-height: 260px;
            }
        }
    
        &__right {
            &_top {
                padding-top: 0;
                border-top: none;
                margin-bottom: 24px;
            }
            &_icon {
                width: 16px;
                height: 16px;
                &.animation-star {
                    display: none;
                }
            }
            &_text {
                max-width: none;
            }
        }
    
        &__advantages {
            &_title {
                font-size: 22px;
                margin-bottom: 16px;
            }
            &_swiper-slide {
                padding: 16px;
                min-height: 240px;
            }
        }

        &__left_text {
            font-size: 16px;
            & p:not(:last-child) {
                margin-bottom: 10px;
            }
            & span {
                font-size: 26px;
                margin-top: 6px;
            }
        }
    }
}