
// prices
.prices {
    &__title {
        margin-bottom: 80px;
    }

    &__table {
        width: 100%;
        margin-bottom: 10px;

        &_body {
            display: flex;
            flex-direction: column;
            gap: 10px;
            width: 100%;
        }

        &_item {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            gap: 0 20px;
            border-radius: 5px;
            padding: 20px;
            background: var(--fon-blokov);
        }

        &_name {
            flex: 1 0 50%;
            font-weight: 400;
            font-size: 20px;
            line-height: 110%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            display: flex;
            align-items: center;
            gap: 12px;
            & svg {
                min-width: 25px;
                transition: all 0.3s ease;
                & > * {
                    transition: all 0.3s ease;
                }
            }
            &.accordion--active {
                & svg {
                    transform: rotateX(180deg);
                    border-radius: 50px;
                    border: 1px solid var(--osnovnoy-tusklyy);
                    & rect {
                        fill: transparent;
                    }
                    & path {
                        fill: var(--osnovnoy-tusklyy);
                    }
                }
            }
        }

        &_price {
            flex: 1 0 25%;
            font-weight: 500;
            font-size: 20px;
            line-height: 120%;
            color: var(--akcentnyy-zelenyy);
        }

        &_btn button {
            padding: 10px 25px;
        }

        &_description {
            flex: 1 0 100%;
            & p:first-child {
                padding-top: 16px;
                border-top: 1px solid #e6e6eb;
                margin-top: 20px;
            }
        }
    }

    &__btn {
        margin-bottom: 40px;
    }

    &__payment {
        border-radius: 5px;
        padding: 24px;
        background: var(--osnovnoy-tusklyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;

        & p {
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            position: relative;
            padding-right: 30px;
            width: fit-content;
            &::after {
                content: "";
                display: block;
                width: 14px;
                height: 14px;
                background: url("./../img/icons/star-2-white.svg") no-repeat center / contain;
                position: absolute;
                top: 8px;
                right: 0;
            }
        }

        &_list {
            display: flex;
            gap: 18px;
            font-style: italic;
            color: var(--shrift-svetlyy);
            & li {
                display: flex;
                align-items: center;
                gap: 8px;
                &:not(:last-child) {
                    padding-right: 18px;
                    border-right: 1px solid rgba(251, 251, 254, 0.15);
                }
            }
        }
    }
}

// media prices
@media (max-width: 1270px) {
    .prices {
        &__payment {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
        }
    }
}
@media (max-width: 930px) {
    .prices {
        &__payment {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
            &_list {
                flex-direction: column;
                gap: 12px;
                & li {
                    &:not(:last-child) {
                        padding-right: 0;
                        border-right: none;
                        padding-bottom: 12px;
                        border-bottom: 1px solid rgba(251, 251, 254, 0.15);
                    }
                }
            }
        }
    }
}
@media (max-width: 768px) {
    .prices {
        &__title {
            margin-bottom: 30px;
        }
        &__table {
            &_item {
                padding: 16px;
                gap: 7px;
            }
            &_name {
                flex: 1 0 100%;
                font-size: 16px;
                justify-content: space-between;
                & svg {
                    min-width: 25px;
                    & rect {
                        display: none;
                    }
                    & path {
                        fill: var(--shrift-temnyy);
                    }
                }
                &.accordion--active {
                    & svg {
                        transform: rotateX(180deg);
                        border: none;
                        & path {
                            fill: var(--shrift-temnyy);
                        }
                    }
                }
            }
            &_price {
                flex: 1 0 45%;
                font-size: 18px;
                order: 3;
            }
            &_btn {
                order: 4;
            }
            &_btn button {
                padding: 6px 18px;
            }
            &_description {
                flex: 1 0 100%;
                order: 2;
                & p:first-child {
                    padding-top: 0;
                    border-top: none;
                    margin-top: 3px;
                }
                & p:last-child {
                    padding-bottom: 16px;
                    border-bottom: 1px solid #e6e6eb;
                    margin-bottom: 16px;
                }
            }
        }
        &__btn {
            margin-bottom: 30px;
        }
        &__payment {
            padding: 24px 16px;
            gap: 28px;
            & p {
                font-size: 22px;
                padding-right: 26px;
                &::after {
                    width: 12px;
                    height: 12px;
                }
            }
        }
    }
}