
// prep
.prep {
    overflow: hidden;
    
    &__swiper {
        height: 100%;
        margin-bottom: 30px;
    }

    &__card {
        border-radius: 5px;
        padding: 20px;
        background: var(--fon-blokov);
        height: auto;

        &_name {
            border: 1px solid var(--osnovnoy-tusklyy);
            border-radius: 5px;
            padding: 10px 16px;
            opacity: 0.8;
            font-weight: 500;
            font-size: 16px;
            line-height: 120%;
            color: var(--osnovnoy-tusklyy);
            font-style: italic;
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 6px;
            margin-bottom: 120px;

            &::before {
                content: "";
                display: block;
                min-width: 8px;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--osnovnoy-tusklyy);
            }
        }

        &_list ul {
            & li {
                &:not(:last-child) {
                    padding-bottom: 20px;
                    border-bottom: 1px solid #e6e6eb;
                    margin-bottom: 16px;
                }

                & p {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 120%;
                    color: var(--shrift-temnyy);
                    margin-bottom: 8px;
                    position: relative;
                    padding-left: 14px;

                    &::before {
                        content: "";
                        display: block;
                        width: 8px;
                        height: 8px;
                        position: absolute;
                        top: 6px;
                        left: 0;
                    }
                }

                &:nth-child(1) p::before {background: url("./../img/icons/star-red.svg") no-repeat center / contain;}
                &:nth-child(2) p::before {background: url("./../img/icons/star-green.svg") no-repeat center / contain;}
                &:nth-child(3) p::before {background: url("./../img/icons/star-yellow.svg") no-repeat center / contain;}

                & span {
                    font-style: italic;
                }
            }
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        gap: 12px;
        &::before {
            content: url("./../img/icons/download.svg");
            display: block;
            margin-top: 2px;
        }
    }
}

// media prep
@media (max-width: 768px) {
    .prep {
        &__swiper {
            margin-bottom: 12px;
        }
    
        &__card {
            padding: 16px;
            &_name {
                padding: 6px 12px;
                font-size: 14px;
                margin-bottom: 50px;
                &::before {
                    min-width: 6px;
                    width: 6px;
                    height: 6px;
                }
            }
            &_list ul {
                & li {
                    &:not(:last-child) {
                        padding-bottom: 14px;
                        margin-bottom: 10px;
                    }
                    & p {
                        font-size: 16px;
                    }
                }
            }
        }
    
        &__btn {
            align-items: center;
        }
    }
    
}