
// articles
.articles {
    padding: 150px 0;
    background: var(--fon-blokov);
    overflow: hidden;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 80px;
    }

    &__btns {
        border-radius: 5px;
        padding: 4px;
        background: var(--fon);
        display: flex;
        gap: 8px;
    }

    &__tab_btn {
        display: block;
        border-radius: 5px;
        padding: 14px 32px;
        font-weight: 500;
        font-size: 20px;
        color: var(--shrift-tekst-na-fone);
        flex-grow: 1;
        transition: all 0.2s ease-in;

        &:hover {
            background: rgba(172, 173, 223, 0.1);
        }

        &.active {
            background: var(--osnovnoy-tusklyy);
            color: var(--shrift-svetlyy);
        }
    }
    
    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
        margin-bottom: 30px;
    }

    &__card {
        border-radius: 5px;
        padding: 28px;
        box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
        background: var(--fon);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 49px;

        &:first-child {
            grid-row: span 3;
        }

        &_date {
            font-size: 16px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--osnovnoy-tusklyy);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 12px;

            &::after {
                content: "";
                display: block;
                width: 12px;
                height: 12px;
                background: url("./../img/icons/star.svg") no-repeat center / contain;
            }
        }

        &_title {
            font-weight: 500;
            font-size: 22px;
            line-height: 120%;
            color: var(--shrift-temnyy);
            margin-bottom: 12px;
        }

        &_description {
            font-style: italic;
            color: var(--shrift-tekst-na-fone);
        }
    }
}

// media articles
@media (max-width: 1024px) {
    .articles {
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
        }
        &__cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;
            margin-bottom: 30px;
        }
        &__card {
            &:first-child {
                grid-row: span 1;
            }
        }
    }
}

@media (max-width: 768px) {
    .articles {
        padding: 70px 0;
        &__top {
            margin-bottom: 30px;
        }
        &__tab_btn {
            padding: 12px 30px;
            font-size: 16px;
        }
        &__cards {
            grid-template-columns: repeat(10, 1fr);
            gap: 10px;
            overflow-x: auto;
            scroll-behavior: smooth;
            scroll-snap-type: x mandatory;
            margin-bottom: 28px;
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
        &__card {
            padding: 16px;
            gap: 70px;
            min-width: 280px;
            scroll-snap-align: start;
            &_date {
                font-size: 14px;
            }
            &_title {
                font-size: 18px;
            }
        }
    }
}