
// doctor
.doctor {
    &__row {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
    &__section-title {
        padding-right: 65px;
    }

    &__main {
        &_photo {
            position: relative;
            background: var(--osnovnoy-tusklyy) url("./../img/doctors/doctor-card-bg.svg") center / cover;
            background-size: 100%;
            height: 100%;
            border-radius: 5px;
            transition: all 0.5s ease;
        }
    
        &_img {
            position: absolute;
            bottom: 0;
            right: 0px;
            left: 0px;
            width: 100%;
            height: calc(100% - 36px);
            display: block;
            margin: 0 auto;
            & img {
                object-position: top;
            }
        }

        &_content {
            border-radius: 5px;
            padding: 40px;
            background: var(--fon-blokov);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 50px;
        }

        &_text {
            margin-bottom: 28px;
        }

        &_list {
            margin-bottom: 52px;
            & li {
                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid  #e6e6eb;
                    margin-bottom: 16px;
                }
                
                & span {
                    display: block;
                    font-style: italic;
                    font-size: 16px;
                    color: var(--shrift-tekst-na-fone);
                    padding-left: 18px;
                    position: relative;
                    margin-bottom: 6px;
        
                    &::before {
                        content: "";
                        display: block;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star.svg") no-repeat center / contain;
                        position: absolute;
                        top: 4px;
                        left: 0;
                    }
                }
    
                & p {
                    font-weight: 500;
                    color: var(--shrift-temnyy);
                }
            }
        }

        &_ratings {
            display: flex;
            gap: 9px;
        }
        &_rating {
            flex: 1 0 30%;
            border-radius: 5px;
            padding: 15px 20px;
            box-shadow: 0 2px 8px 0 rgba(46, 55, 73, 0.02);
            background: var(--fon);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;

            & p {
                font-weight: 500;
                color: var(--shrift-temnyy);
                display: flex;
                align-items: center;
                gap: 8px;
                &::after {
                    content: "";
                    display: block;
                    width: 15px;
                    height: 15px;
                    background: url("./../img/ratings/star.svg") no-repeat center / contain;
                }
            }

            &-logo {
                max-height: 15px;
                object-fit: contain;
            }
        }

        &_cost {
            color: var(--shrift-temnyy);
            line-height: 105%;
            letter-spacing: -0.02em;
            width: fit-content;
            position: relative;
            margin-bottom: 35px;
            & span {
                position: relative;
                z-index: 2;
            }
            & i {
                font-style: italic;
                font-weight: 500;
                font-size: 28px;
            }
    
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 6px;
                background: var(--akcentnyy-zelenyy);
                position: absolute;
                bottom: 2px;
                left: 0;
            }
        }
    }

    &__shedule {
        &_list ul {
            display: flex;
            flex-direction: column;
            gap: 12px;
            & li {
                padding-top: 12px;
                border-top: 1px solid var(--seryy);
                line-height: 120%;
                color: var(--shrift-temnyy);
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 20px;
                & p:last-child {
                    font-weight: 500;
                    &.weekand {
                        color: var(--osnovnoy-tusklyy);
                    }
                }
            }
        }
    }

    &__educations {
        padding: 150px 0;
        background: var(--fon-blokov);
        overflow: hidden;

        &_top {
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            gap: 30px;
            margin-bottom: 80px;
        }

        &_tab-btns {
            display: flex;
            gap: 6px;
        }
        &_tab-btn {
            border: 1px solid rgba(172, 173, 223, 0.8);
            border-radius: 5px;
            padding: 12px 20px;
            font-weight: 500;
            color: var(--osnovnoy-tusklyy);
            transition: all 0.2s ease-in;
            
            &.active {
                background: var(--osnovnoy-tusklyy);
                color: var(--shrift-svetlyy);
            }
        }

        &_swiper {
            overflow: visible;
        }

        &_card {
            padding-right: 20px;
            border-right: 1px solid #e6e6eb;
            height: auto;
            & span {
                display: block;
                font-weight: 500;
                font-size: 24px;
                line-height: 120%;
                color: var(--shrift-temnyy);
                margin-bottom: 5px;
                padding-left: 18px;
                position: relative;
                margin-bottom: 6px;
    
                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star.svg") no-repeat center / contain;
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }
    }

    &__diplomas {
        &_title {
            margin-bottom: 80px;
        }

        &_cards {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 20px;
        }
        &_card {
            border-radius: 5px;
            padding: 28px;
            background: var(--fon-blokov);
            min-height: 425px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
        }
        &_tag {
            padding: 8px 16px;
            border: 1px solid rgba(172, 173, 223, 0.5);
            border-radius: 5px;
            font-size: 16px;
            color: var(--osnovnoy-tusklyy);
            width: fit-content;
            margin-bottom: 16px;
        }
        &_name {
            font-weight: 500;
            font-size: 22px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
        }
        &_link {
            padding: 24px;
            background: var(--osnovnoy-tusklyy);
            &:hover {
                background: var(--osnovnoy-tusklyy);
                opacity: 0.7;
            }
        }
    }

    &__works {
        padding: 150px 0;
        background: var(--fon-blokov);
        &_list ul {
            max-height: 390px;
            overflow: hidden;
            margin-bottom: 24px;
            & li {
                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid var(--seryy);
                    margin-bottom: 16px;
                }
                & a {
                    font-weight: 400;
                    font-size: 22px;
                    line-height: 120%;
                    color: var(--shrift-temnyy);
                    transition: all 0.2s ease-in;
                    padding-left: 18px;
                    position: relative;
                    &::before {
                        content: "";
                        display: block;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star.svg") no-repeat center / contain;
                        position: absolute;
                        top: 4px;
                        left: 0;
                    }
                    &:hover {
                        color: var(--osnovnoy-tusklyy);
                    }
                }
            }
        }
        &_btn {
            color: var(--akcentnyy-zelenyy);
            padding-right: 18px;
            position: relative;
            &::after {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/arrow-bottom-green.svg") no-repeat center / contain;
                position: absolute;
                top: 6px;
                right: 0;
            }
        }
    }
}


// media doctor
@media (max-width: 1280px) {
    .doctor {
        &__diplomas {
            &_cards {
                grid-template-columns: repeat(3, 1fr);
                gap: 20px;
            }
        }
    }
}

@media (max-width: 1024px) {
    .doctor {
        &__row {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
        &__section-title {
            margin-bottom: 10px;
            padding: 0;
        }
        &__main {
            &_photo {
                min-height: 650px;
            }
            &_wrap {
                display: contents;
            }
            &_text {
                order: 2;
                margin-bottom: 0;
            }
            &_list {
                order: 1;
                margin-bottom: 0;
            }
            &_ratings {
                order: 4;
            }
            &_bottom {
                order: 3;
            }
        }
        &__educations {
            &_top {
                flex-direction: column;
                align-items: stretch;
                gap: 24px;
            }
        }
        &__diplomas {
            &_cards {
                grid-template-columns: repeat(2, 1fr);
                gap: 12px;
            }
            &_card {
                min-height: 325px;
            }
        }
    }
}

@media (max-width: 768px) {
    .doctor {
        &__row {
            gap: 20px;
        }
    
        &__main {
            &_photo {
                min-height: 400px;
            }
    
            &_content {
                padding: 24px 16px;
                gap: 24px;
            }
            &_wrap {
                display: contents;
            }
    
            &_text {
                order: 2;
                margin-bottom: 0;
            }
    
            &_list {
                order: 1;
                margin-bottom: 0;
                & li {
                    &:not(:last-child) {
                        padding-bottom: 14px;
                        margin-bottom: 10px;
                    }
                    & span {
                        font-size: 14px;
                        margin-bottom: 4px;
                    }
                }
            }
    
            &_ratings {
                order: 4;
                flex-direction: column;
                gap: 4px;
            }
            &_rating {
                padding: 10px 13px;
            }
            &_bottom {
                order: 3;
            }
            &_cost {
                margin-bottom: 20px;
                & i {
                    font-size: 24px;
                }
            }
        }

        &__educations {
            padding: 70px 0;
            &_top {
                margin-bottom: 30px;
            }
            &_tab-btns {
                overflow-x: auto;
                &::-webkit-scrollbar {
                    width: 0;
                    height: 0;
                }
            }
            &_tab-btn {
                padding: 12px 16px;
                text-wrap: nowrap;
            }
            &_card {
                & span {
                    font-size: 18px;
                }
            }
        }

        &__diplomas {
            &_title {
                margin-bottom: 30px;
            }
            &_cards {
                grid-template-columns: repeat(20, 1fr);
                gap: 8px;
                overflow-x: auto;
                scroll-snap-type: x mandatory;
                scroll-behavior: smooth;
                &::-webkit-scrollbar {
                    width: 0;
                    height: 0;
                }
            }
            &_card {
                padding: 16px;
                min-height: 280px;
                min-width: 280px;
                width: calc(100vw - 60px);
                scroll-snap-align: start;
            }
            &_tag {
                font-size: 14px;
                margin-bottom: 12px;
            }
            &_name {
                font-size: 20px;
            }
            &_link {
                padding: 20px;
            }
        }
    
        &__works {
            padding: 70px 0;
            &_list ul {
                max-height: 400px;
                margin-bottom: 20px;
            }
        }
    }
}



@media (max-width: 500px) {
    .doctor {
        &__main {
            &_photo {
                min-height: 300px;
            }
        }
    }
}