
// brigades
.brigades {
    
    &__title {
        margin-bottom: 80px;
        max-width: 900px;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__card {
        width: calc(33% - 10px);
        border-radius: 5px;
        padding: 24px 24px 20px;
        color: var(--shrift-svetlyy);
        min-height: 404px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        
        &_img {
            width: 100%;
        }
        &_picture {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                min-height: 100px;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: left;
            }
        }

        &_map {
            align-items: center;
            justify-content: center;
        }
        
        &:first-child {
            background: var(--osnovnoy-tusklyy);
            & ul li p {
                color: #dfdff2;
            }
        }

        &:last-child {
            background: var(--akcentnyy-zelenyy);
            & ul li p {
                color: #c9f0ec;
            }
        }

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 25px;
            margin-bottom: 32px;
        }

        &_title {
            font-weight: 500;
            font-size: 28px;
            line-height: 105%;
            letter-spacing: -0.02em;
        }

        &_list ul {
            & li {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 25px;
                font-style: italic;

                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    margin-bottom: 12px;
                }
            }
        }
    }

}

// media brigades
@media (max-width: 1100px) {
    .brigades {
        &__row {
            flex-direction: column;
            gap: 28px;
        }
        &__card {
            width: 100%;
            min-height: auto;
            gap: 24px;
        }
    }
}
@media (max-width: 786px) {
    .brigades {
        &__title {
            margin-bottom: 30px;
        }
        &__map img {
            max-height: 300px;
            object-fit: cover;
        }
        &__card {
            padding: 16px;
            &_top {
                margin-bottom: 28px;
            }
            &_title {
                font-size: 22px;
            }
            &_list ul {
                & li {
                    &:not(:last-child) {
                        padding-bottom: 12px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    
    }
}