
// doctors 
.doctors {
    overflow: hidden;
    
    &__top {
        display: flex;
        align-items: center;
        gap: 50px;
        margin-bottom: 80px;

        &_title {
            flex-basis: 50%;
        }
        
        &_text {
            flex-basis: 40%;
            font-style: italic;
        }
    }

    &__swiper {
        margin-bottom: 40px;
        overflow: visible;
    }

    &__card {
        display: block;

        &:hover {
            & .doctors__card_photo {
                background-size: 120%;
            }
        }

        &_photo {
            position: relative;
            background: var(--osnovnoy-tusklyy) url("./../img/doctors/doctor-card-bg.svg") center / cover;
            background-size: 100%;
            height: 100%;
            min-height: 400px;
            max-height: 400px;
            margin-bottom: 20px;
            transition: all 0.5s ease;
        }

        &_img {
            position: absolute;
            bottom: 0;
            right: 20px;
            left: 20px;
            width: calc(100% - 40px);
            height: calc(100% - 36px);
            display: block;
            margin: 0 auto;
            & img {
                object-position: top;
            }
        }

        &_experience {
            border-radius: 0 5px;
            padding: 10px 15px;
            background: var(--akcentnyy-zheltyy);
            width: fit-content;
            font-weight: 500;
            font-size: 16px;
            line-height: 105%;
            color: var(--shrift-temnyy);
            position: absolute;
            top: 0;
            right: 0;
        }

        &_fio {
            font-weight: 500;
            font-size: 28px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            margin-bottom: 12px;
        } 

        &_post {
            font-style: italic;
            color: var(--shrift-tekst-na-fone);
        }
    }
}

.doctors-page {
    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 64px 20px;
    }
    &__link {
        display: none;
    }
}

// media doctors
@media (max-width: 1240px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 64px 20px;
        }
    }
}
@media (max-width: 1000px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 64px 20px;
        }
    }
}
@media (max-width: 768px) {
    .doctors {
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
            margin-bottom: 24px;
            &_title, &_text {
                flex-basis: 100%;
            }
        }
    
        &__swiper {
            margin-bottom: 28px;
        }
    
        &__card {
            &_photo {
                min-height: 296px;
                max-height: 296px;
                margin-bottom: 16px;
            }
    
            &_img {
                right: 8px;
                left: 8px;
                width: calc(100% - 16px);
            }
    
            &_experience {
                padding: 7px 10px;
                font-size: 14px;
            }
    
            &_fio {
                font-size: 22px;
                margin-bottom: 8px;
            }
        }
    }
}

@media (max-width: 500px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 24px;
        }
        &__link {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 16px;
            line-height: 120%;
            color: var(--akcentnyy-zelenyy);
            padding-top: 12px;
            border-top: 1px solid var(--seryy);
            margin-top: 16px;
            &::after {
                content: url("./../img/icons/arrow-right-green.svg");
            }
        }
    }
}