.popup-change {
    &__content {
        border-radius: 5px;
        padding: 100px 60px;
        max-width: 800px;
        background: var(--osnovnoy-tusklyy);
        color: #dfdff2;
        position: relative;
        overflow: hidden;
    }

    &__bg {
        &_item {
            width: 100%;
            position: absolute;
            left: 0;
            & img {
                width: 100%;
                height: 100%;
                min-height: 75px;
                object-fit: cover;
            }
            &:first-child {
                top: -22px;
            }
            &:last-child {
                bottom: -22px;
            }
        }
    }

    &__text {
        margin-bottom: 64px;
    }

    &__tag {
        border: 1px solid rgba(251, 251, 254, 0.5);
        border-radius: 5px;
        padding: 10px 20px;
        color: var(--shrift-svetlyy);
        width: fit-content;
        margin-bottom: 16px;
    }

    &__title {
        font-weight: 500;
        font-size: 52px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-svetlyy);
        margin-bottom: 20px;

        & i {
            display: block;
        }
    }

    &__subtitle {
        font-style: italic;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(251, 251, 254, 0.15);
        margin-bottom: 14px;
    }

    &__phone {
        display: flex;
        align-items: flex-end;
        gap: 16px;
        & p {
            font-style: italic;
        }
        & a {
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 100%;
                height: 5px;
                background: var(--akcentnyy-zelenyy);
                position: absolute;
                bottom: 4px;
                left: 11px;
                z-index: -1;
            }
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 8px;

        & .input-wrap .label {
            color: #dfdff2;
        }

        & input::placeholder {
            color: #dfdff2;
        }
    }
}




.popup-change-response {
    &__wrapper {
        & h3 {
            font-weight: 500;
            font-size: 52px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            text-align: center;
            margin-bottom: 30px;
        }

        & p {
            text-align: center;
            font-style: italic;
        }
    }
    &__btn {
        margin: 60px auto 0;
        width: 80%;
    }
}




// media popup-change-1
@media (max-width: 768px) {
    .popup-change {
        &__content {
            padding: 40px 16px;
        }
        &__text {
            margin-bottom: 32px;
        }
        &__tag, &__phone {
            display: none;
        }
        &__title {
            font-size: 30px;
            margin-bottom: 12px;
        }
        &__subtitle {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
    }

    .popup-change-response {
        &__content {
            padding: 60px 16px;
        }
        &__wrapper {
            & h3 {
                font-size: 30px;
                margin-bottom: 12px;
            }
        }
        &__btn {
            margin: 30px auto 0;
            width: 100%;
        }
    }
}
