
// popup-test
.popup-test {
    &__content {
        border-radius: 5px;
        padding: 60px;
        max-width: 750px;
        background: var(--fon-blokov);
        color: var(--shrift-temnyy);
        position: relative;
        overflow: hidden;
    }

    &__title {
        font-weight: 500;
        font-size: 42px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-temnyy);
        margin-bottom: 52px;
    }

    &__form {
        margin-bottom: 24px;
        &_text {
            font-weight: 500;
            font-size: 22px;
            line-height: 120%;
            margin-bottom: 20px;
        }

        &_inner {
            display: flex;
            flex-direction: column;
            gap: 8px;
            margin-bottom: 12px;
        }

        & .radio {
            & label {
                border: 1px solid rgba(172, 173, 223, 0.3);
                border-radius: 5px;
                padding: 20px;
                color: var(--shrift-temnyy);
                display: flex;
                align-items: center;
                gap: 12px;
                transition: all 0.3s ease;
                &::before {
                    content: "";
                    min-width: 10px;
                    width: 10px;
                    height: 10px;
                    border-radius: 100%;
                    background: rgba(172, 173, 223, 0.3);
                    display: inline-block;
                    transition: 0.25s ease;
                }
                &:hover {
                    border: 1px solid var(--osnovnoy-tusklyy);
                }
            }
            & input[type="radio"] {
                display: none;
            }
            & input[type="radio"]:checked + label::before {
                background-color: var(--osnovnoy-tusklyy);
            }
            & input[type="radio"]:checked + label {
                border: 1px solid var(--osnovnoy-tusklyy);
            }
        }

        & .test__btn_next {
            cursor: pointer;
        }
    }
}

.test__item.hidden {
    display: none;
}

.pagination {
    display: flex;
    gap: 9px;
    &__item {
        border-radius: 10px;
        width: 100%;
        height: 5px;
        background: var(--osnovnoy-tusklyy);
        opacity: 0.2;
        &.active {
            opacity: 1;
        }
    }
}


.popup-test-response {
    &.green {
        & .popup-test-response__content {
            background: var(--akcentnyy-zelenyy);
        }
        & .popup-test-response__text {
            color: #c9f0ec;
        }
    }

    &.purple {
        & .popup-test-response__content {
            background: var(--osnovnoy-tusklyy);
        }
        & .popup-test-response__text {
            color: #dfdff2;
        }
    }
    
    &.red {
        & .popup-test-response__content {
            background: var(--akcentnyy-krasnyy);
        }
        & .popup-test-response__text {
            color: #f2c2c5;
        }
    }

    &__content {
        border-radius: 5px;
        padding: 60px;
        max-width: 750px;
        color: var(--shrift-svetlyy);
        position: relative;
        overflow: hidden;
    }

    &__text {
        margin-bottom: 100px;
        &_p {
            margin-bottom: 8px;
        }
        &_title {
            font-weight: 500;
            font-size: 42px;
            color: var(--shrift-svetlyy);
            line-height: 105%;
            letter-spacing: -0.02em;
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(251, 251, 254, 0.15);
            margin-bottom: 16px;
        }
        &_description {
            font-style: italic;
        }
    }

    &__form {
        &_text {
            font-style: italic;
            font-weight: 500;
            font-size: 22px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            margin-bottom: 20px;
        }
        &_input {
            margin-bottom: 12px;
        }
        &_btn {
            width: 100%;
        }
    }
}


// media popup-test
@media (max-width: 768px) {
    .popup-test {
        &__content {
            padding: 40px 16px;
        }
        &__title {
            font-size: 30px;
            margin-bottom: 48px;
        }
        &__form {
            &_text {
                font-size: 18px;
                margin-bottom: 16px;
            }
            &_inner {
                gap: 6px;
                margin-bottom: 16px;
            }
            & .radio {
                & label {
                    padding: 16px;
                    gap: 8px;
                    &::before {
                        min-width: 8px;
                        width: 8px;
                        height: 8px;
                    }
                }
            }
        }
    }

    .popup-test-response {
        &__content {
            padding: 40px 16px;
        }
        &__text {
            margin-bottom: 80px;
            &_title {
                font-size: 22px;
                padding-bottom: 10px;
                margin-bottom: 10px;
            }
        }
        &__form {
            &_text {
                font-size: 16px;
                margin-bottom: 16px;
            }
        }
    }
}
