
// faq 
.faq {
    &__title {
        margin-bottom: 80px;
    }

    &__list {
        margin-bottom: 30px;
    }

    &__item {
        display: flex;
        justify-content: space-between;
        gap: 30px;
        text-align: left;
        padding: 24px 0;
        border-bottom: 1px solid var(--seryy);
        &:first-child {
            border-top: 1px solid var(--seryy);
        }

        &.accordion--active {
            & .faq__item_btn {
                border-radius: 50px;
                border: 1px solid var(--osnovnoy-tusklyy);
                & svg path {
                    fill: var(--osnovnoy-tusklyy);
                }
                & svg rect {
                    fill: transparent;
                }
            }
        }

        &_question {
            flex-basis: 40%;
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            color: var(--shrift-temnyy);
        }

        &_answer {
            flex-basis: 40%;
            max-width: 560px;
            font-style: italic;
            font-size: 16px;
            line-height: 120%;
        }

        &_btn {
            min-width: 40px;
            width: 40px;
            height: 40px;
            & svg {
                & > * {
                    transition: all 0.2s ease-in;
                }
            }
        }
    }

    &__answered {
        display: block;
        border-radius: 5px;
        padding: 20px 24px 24px 24px;
        background: #78cfc7;
        margin-top: 20px;

        &_top {
            font-style: italic;
            font-size: 16px;
            line-height: 120%;
            color: #c9f0ec;
            padding-bottom: 12px;
            border-bottom: 1px solid rgba(251,251,254,0.15);
            margin-bottom: 16px;
        }

        &_doctor {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &_photo {
            display: block;
            border-radius: 40px;
            min-width: 70px;
            width: 70px;
            height: 70px;
            background: #fff;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }

        &_fio {
            & p {
                font-weight: 500;
                font-size: 22px;
                line-height: 120%;
                color: var(--shrift-svetlyy);
            }
            & span {
                color: #c9f0ec;
                font-style: italic;
            }
        }
    }
}

// media faq
@media (max-width: 1024px) {
    .faq {
        &__item {
            flex-wrap: wrap;
            gap: 0 30px;
            &.accordion--active {
                gap: 16px 30px;
            }
            &_question {
                flex: 1 0 70%;
            }
            &_answer {
                flex-basis: 100%;
                order: 3;
                max-width: none;
            }
        }
    }    
}

@media (max-width: 768px) {
    .faq {
        &__title {
            margin-bottom: 30px;
        }
        &__list {
            margin-bottom: 28px;
        }
        &__item {
            padding: 16px 0 20px;
            &_question {
                font-size: 18px;
            }
            &_btn {
                min-width: 30px;
                width: 30px;
                height: 30px;
            }
        }
        &__answered {
            padding: 16px;
            margin-top: 20px;
            &_top {
                font-size: 14px;
                padding-bottom: 10px;
                margin-bottom: 12px;
            }
            &_doctor {
                gap: 12px;
            }
            &_photo {
                min-width: 50px;
                width: 50px;
                height: 50px;
            }
            &_fio {
                & p {
                    font-size: 18px;
                }
            }
        }
    }
}