
// alcoholism
.alcoholism {
    padding: 150px 0;
    background: var(--fon-blokov);

    &__top {
        display: flex;
        gap: 30px;
        margin-bottom: 80px;
    }
    &__title {
        flex-basis: 50%;
    }

    &__subtitle {
        flex-basis: 45%;
        max-width: 650px;
        font-style: italic;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 5px;
        padding: 32px;
        box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
        background: var(--fon);
        height: fit-content;

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 12px;
        }

        &_icon {
            display: block;
            width: 50px;
            height: 50px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &_name {
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            color: var(--shrift-temnyy);
            margin-bottom: 10px;
        }

        &_info {
            font-style: italic;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            padding-bottom: 16px;
            border-bottom: 1px solid var(--seryy);
            margin-bottom: 12px;
        }

        &_cost {
            font-weight: 500;
            font-size: 20px;
            line-height: 120%;
            color: var(--shrift-temnyy);
            position: relative;
            & span {
                position: relative;
                z-index: 2;
            }
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 4px;
                background: var(--akcentnyy-zelenyy);
                position: absolute;
                bottom: 3px;
                left: 0;
            }
        }

        &_text {
            font-style: italic;
            color: var(--osnovnoy-tusklyy);
        }

        &_content {
            border: 1px solid rgba(172, 173, 223, 0.1);
            border-radius: 4px;
            padding: 32px 24px;
            background: var(--fon-blokov);
            display: flex;
            flex-wrap: wrap;
            gap: 32px 28px;
            margin: 32px 0 4px;
        }

        &_list {
            flex: 1 0 45%;
            & p {
                font-weight: 500;
                font-size: 16px;
                line-height: 120%;
                font-style: italic;
                margin-bottom: 16px;
                color: var(--osnovnoy-tusklyy);
            }
            & li {
                line-height: 120%;
                color: var(--shrift-temnyy);
                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid #e6e6eb;
                    margin-bottom: 12px;
                }
            }
            & ul li {
                position: relative;
                padding-left: 18px;
                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star-green.svg") no-repeat center / contain;
                    position: absolute;
                    top: 5px;
                    left: 0;
                }
            }

            & ol {
                list-style-type: none;
                counter-reset: item;
                & li {
                    display: flex;
                    gap: 16px;
                    &::before {
                        content: counter(item, decimal-leading-zero); 
                        counter-increment: item;
                        font-style: italic;
                        font-weight: 500;
                        font-size: 20px;
                        line-height: 120%;
                        color: var(--osnovnoy-tusklyy);
                    }
                }
            }



            &.purple {
                & p {
                    color: var(--osnovnoy-tusklyy);
                }
            }
            &.green {
                & p {
                    color: var(--akcentnyy-zelenyy);
                }
                & ul li::before {
                    background: url("./../img/icons/star-green.svg") no-repeat center / contain;
                }
            }
            &.red {
                & p {
                    color: var(--akcentnyy-krasnyy);
                }
                & ul li::before {
                    background: url("./../img/icons/star-red.svg") no-repeat center / contain;
                }
                flex: 1 0 100%;
            }
        }
    }

}

// media alcoholism
@media (max-width: 1024px) {
    .alcoholism {
        &__top {
            flex-direction: column;
            gap: 20px;
        }
        &__subtitle {
            max-width: none;
        }
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
    }
}

@media (max-width: 768px) {
    .alcoholism {
        padding: 70px 0;
        &__top {
            gap: 12px;
            margin-bottom: 30px;
        }
        &__cards {
            gap: 8px;
        }
    
        &__card {
            padding: 16px;
            &_icon {
                width: 36px;
                height: 36px;
            }
            &_name {
                font-size: 20px;
            }
            &_info {
                padding-bottom: 12px;
                margin-bottom: 10px;
                font-size: 16px;
            }
            &_cost {
                font-size: 16px;
            }
            &_content {
                padding: 28px 12px;
                gap: 24px;
                margin: 24px 0 4px;
            }
    
            &_list {
                flex: 1 0 100%;
                & p {
                    margin-bottom: 12px;
                }
                & li:not(:last-child) {
                    padding-bottom: 14px;
                    margin-bottom: 10px;
                }
                & ul li {
                    padding-left: 14px;
                    &::before {
                        width: 8px;
                        height: 8px;
                        top: 4px;
                    }
                }
    
                & ol {
                    & li {
                        gap: 10px;
                        &::before {
                            font-size: 18px;
                        }
                    }
                }
            }
        }
    }
}