
// calculator
.calculator {
    padding: 150px 0;
    background: var(--akcentnyy-zelenyy);
    position: relative;
    overflow: hidden;

    &__bg {
        position: absolute;
        bottom: 0;
        left: 0;
    }

    &__row {
        display: flex;
        gap: 60px;
    }

    &__title {
        flex: 1 0 42%;
        color: var(--shrift-svetlyy);
    }

    &__inner {
        flex: 1 0 45%;
        position: relative;
        z-index: 2;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 28px;
        margin-bottom: 12px;
    }

    &__result {
        border-radius: 5px;
        padding: 24px 32px;
        background: var(--fon);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-top: 4px;

        &_item {
            font-weight: 400;
            font-size: 16px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-tekst-na-fone);

        }
        
        &_top {
            display: flex;
            gap: 12px;
            margin-bottom: 6px;
            font-weight: 500;
            font-size: 32px;
            line-height: 105%;
            color: var(--shrift-temnyy);
            font-style: italic;
        }
    }
}

// media calculator
@media (max-width: 1024px) {
    .calculator {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
    }
}

@media (max-width: 768px) {
    .calculator {
        padding: 70px 0;
        &__bg {
            display: none;
        }
        &__row {
            gap: 30px;
        }
        &__form {
            gap: 20px;
        }
        &__result {
            padding: 16px;
            &_item {
                font-size: 14px;
            }
            &_top {
                gap: 6px;
                font-size: 20px;
                & img {
                    width: 18px;
                    height: 18px;
                    object-fit: contain;
                }
            }
            &_icon {
                display: none;
            }
        }
    }
    
}