
// installment
.installment {
    padding: 150px 0;
    background: var(--akcentnyy-krasnyy);


    &__row {
        display: flex;
        justify-content: space-between;
        gap: 50px;
    }
    
    &__name {
        font-weight: 500;
        font-size: 32px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-svetlyy);
        padding-left: 50px;
        margin-bottom: 24px;
    }


    &__text {
        flex-basis: 573px;
    }

    &__picture {
        width: fit-content;
        & img {
            max-width: 880px;
        }
    }

    &__list {
        & ul {
            padding-left: 50px;
            margin-bottom: 24px;
            display: flex;
            flex-direction: column;
            gap: 4px;
            & li {
                border-radius: 5px;
                padding: 20px;
                background: rgba(251, 251, 254, 0.1);
                color: #f2c2c5;
                position: relative;

                &::before{
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                    position: absolute;
                    top: 20px;
                    right: 20px;
                }

                & span {
                    display: block;
                    font-weight: 500;
                    font-size: 40px;
                    line-height: 105%;
                    letter-spacing: -0.02em;
                    color: var(--shrift-svetlyy);
                    margin-bottom: 8px;
                }
            }
        }
    }
}

// media installment
@media (max-width: 1024px) {
    .installment {
        &__row {
            display: block;
        }
        &__text {
            flex-basis: auto;
        }
        &__picture {
            display: none;
        }
    }
}
@media (max-width: 768px) {
    .installment {
        padding: 70px 0;
        &__name {
            font-size: 24px;
            padding-left: 38px;
        }
        &__list {
            & ul {
                padding-left: 38px;
                & li {
                    border-radius: 5px;
                    padding: 16px;
    
                    &::before{
                        width: 8px;
                        height: 8px;
                        top: 16px;
                        right: 16px;
                    }
                }
            }
        }
    }
}