
// familiar
.familiar {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__cards {
        list-style-type: none;
        counter-reset: item;
    }

    &__card {
        padding: 23px;
        border-radius: 5px;
        background: var(--fon-blokov);
        font-size: 24px;
        line-height: 120%;
        color: var(--shrift-temnyy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        position: relative;
        overflow: hidden;
        min-height: 300px;

        &::before {
            content: counter(item);
            counter-increment: item;
            display: block;
            min-width: 44px;
            width: 44px;
            height: 44px;
            font-weight: 500;
            font-size: 18px;
            color: var(--shrift-svetlyy);
            background: url("./../img/icons/star-2-green.svg") no-repeat center / contain;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &::after {
            content: "";
            display: block;
            width: 269px;
            height: 97px;
            background: url("./../img/familiar/familiar-icon-bg.svg") no-repeat center / contain;
            position: absolute;
        }

        &:nth-child(1)::after {top: 93px;left: -14px;}
        &:nth-child(2n)::after {top: 0; left: 160px; transform: rotate(-90deg);}
        &:nth-child(3n)::after {top: 95px; left: -95px; transform: rotate(90deg);}
        &:nth-child(4n)::after {top: 95px; left: -95px; transform: rotate(180deg);}
        &:nth-child(5n)::after {top: -16px; left: 180px;}
        &:nth-child(6n)::after {top: 0; left: -50px; transform: rotate(90deg);}
        &:nth-child(7n)::after {top: 10px; left: -105px; transform: rotate(180deg);}
    }
}

// media familiar
@media (max-width: 768px) {
    .familiar {

        &__card {
            padding: 16px;
            font-size: 16px;
            min-height: 200px;
    
            &::before {
                min-width: 32px;
                width: 32px;
                height: 32px;
                font-size: 14px;
            }
    
            &::after {
                content: "";
                display: block;
                width: 269px;
                height: 97px;
                background: url("./../img/familiar/familiar-icon-bg.svg") no-repeat center / contain;
                position: absolute;
            }
    
            &:nth-child(1)::after {top: 93px;left: -14px;}
            &:nth-child(2n)::after {top: 0; left: 160px; transform: rotate(-90deg);}
            &:nth-child(3n)::after {top: 95px; left: -95px; transform: rotate(90deg);}
            &:nth-child(4n)::after {top: 95px; left: -95px; transform: rotate(180deg);}
            &:nth-child(5n)::after {top: -16px; left: 180px;}
            &:nth-child(6n)::after {top: 0; left: -50px; transform: rotate(90deg);}
            &:nth-child(7n)::after {top: 10px; left: -105px; transform: rotate(180deg);}
        }
    }
}