
/* Общие glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
// html.burger-lock::before {
//     opacity: 1;
// }
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

body {
    margin: 0;
    //overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--shrift-tekst-na-fone);
    background: var(--fon);
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}

.section-offset {
    margin-bottom: 150px;
}

.section__top {
    border-bottom: 1px solid var(--seryy);
    margin-bottom: 60px;
    &_title {
        margin-bottom: 52px;
        & span {
            font-style: italic;
        }
    }
}




/*  buttons */
.primary-btn, .secondary-btn, .tertiary-btn, .form-btn-green, .form-btn-white {
    display: block;
    border-radius: 5px;
    padding: 16px 30px 20px 30px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    text-align: center;
    transition: all 0.2s ease-in;
}
@media (hover: hover) {
    .primary-btn, .secondary-btn, .tertiary-btn {
        &:hover::after {
            transform: skewX(-45deg) scale(1, 1);
            transition: all 0.3s ease-out;
        }
    }
}

.primary-btn {
    padding: 29px 30px 30px;
    background: var(--akcentnyy-zelenyy);
    color: var(--shrift-svetlyy);
    width: 100%;
    &:active {
        background: #83d2cc;
    }
}
@media (hover: hover) {
    .primary-btn:hover {
        background: #8fe1da;
    }
}

.secondary-btn {
    border: 1px solid var(--akcentnyy-zelenyy);
    padding: 24px;
    color: var(--akcentnyy-zelenyy);
    text-align: center;
    width: 100%;
    &:active {
        background: rgba(114, 206, 198, 0.8);
        color: var(--shrift-svetlyy);
    }
}
@media (hover: hover) {
    .secondary-btn:hover {
        background: var(--akcentnyy-zelenyy);
        color: var(--shrift-svetlyy);
    }
}

.tertiary-btn {
    border: 1px solid var(--shrift-svetlyy);
    padding: 24px;
    border-radius: 5px;
    color: var(--shrift-svetlyy);
    width: 100%;
    &:active {
        background: rgba(251, 251, 254, 0.8);
        color: var(--osnovnoy-tusklyy);
    }
}
@media (hover: hover) {
    .tertiary-btn:hover {
        background: var(--shrift-svetlyy);
        color: var(--osnovnoy-tusklyy);
    }
}

.form-btn-green {
    border: 1px solid #72cfc6;
    color: var(--akcentnyy-zelenyy);
    &:active {
        background: rgba(114, 206, 198, 0.8);
        color: var(--shrift-svetlyy);
    }
}
@media (hover: hover) {
    .form-btn-green:hover {
        background: var(--akcentnyy-zelenyy);
        color: var(--shrift-svetlyy);
    }
}

.form-btn-white {
    border: 1px solid var(--fon);
    background: var(--fon);
    color: var(--shrift-temnyy);
    &:active {
        background-color: transparent;
        color: var(--shrift-svetlyy);
        opacity: 0.8;
    }
}
@media (hover: hover) {
    .form-btn-white:hover {
        background-color: transparent;
        color: var(--shrift-svetlyy);
    }
}


.btn-more {
    display: block;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--shrift-svetlyy);
    position: relative;
    z-index: 2;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 11px;
        height: 10px;
        background: url("./../img/icons/arrow-bottom-white.svg") no-repeat center / contain;
        margin-left: 8px;
        transition: all 0.3s ease;
    }

    &.show {
        &::after {
            transform: rotateX(180deg);
        }
        &::before {
            display: none;
        }
    }
}
@media (hover: hover) {
    .btn-more {
        &:hover {
            opacity: 0.5;
            &::after {
                transform: translate(0, 4px);
            }
        }
        &.show:hover {
            &::after {
                transform: translate(0, -4px) rotate(180deg);
            }
        }
    }
}

.content-more {
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: block;
        width: 100%;
        height: 131px;
        background: linear-gradient(360deg, #acaddf 36.5%, rgba(172, 173, 223, 0) 100%);
        position: absolute;
        bottom: 0;
        left: 0;
    }

    &.show {
        &::after {
            display: none;
        }
    }
}



