/*  titles  */
.title, .title-litle, .title-ellipse {
    font-family: var(--second-family);
    font-weight: 500;
    line-height: 105%;
    letter-spacing: -0.02em;
}

.title {
    font-size: 64px;
    color: var(--shrift-temnyy);
    & span {
        color: var(--osnovnoy-tusklyy);
    }
}
.title-litle {
    font-size: 20px;
    color: var(--shrift-svetlyy);
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 52px;

    &::before {
        content: "";
        display: block;
        min-width: 10px;
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background-color: var(--shrift-svetlyy);
    }
}
.title-ellipse {
    font-size: 42px;
    color: var(--shrift-temnyy);
    position: relative;
    min-height: 160px;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 40px;

    & span {
        color: var(--akcentnyy-zelenyy);
    }

    & p {
        text-align: center;
        &::after {
            content: "?";
            color: var(--akcentnyy-zelenyy);
            transform: rotate(15deg);
            margin-left: 8px;
        }
    }

    &.red {
        & span {
            color: var(--akcentnyy-krasnyy);
        }
        & p::after {
            content: none;
        }
    }

    &.no-mark {
        & p::after {
            content: none;
        }
    }

    & svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        min-width: calc(100% + 200px);
        min-height: 100%;
    }

    &__svg-mob {
        display: none;
    }
}


.picture {
    display: block;
    width: 100%;
    min-height: 100%;
    border-radius: 5px;
    overflow: hidden;
    & img {
        display: block;
        border-radius: 5px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}





/*  form  */

.input, .textarea, .select {
    width: 100%;
    border: 1px solid rgba(251, 251, 254, 0.4);
    border-radius: 5px;
    padding: 24px;
    font-weight: 400;
    font-size: 18px;
    line-height: 105%;
    letter-spacing: -0.02em;
    color: var(--shrift-svetlyy);
    transition: all 0.3s ease;
}
.input::placeholder, .textarea::placeholder {
    font-weight: 400;
    font-size: 18px;
    line-height: 105%;
    letter-spacing: -0.02em;
    color: #c9f0ec;
}
.input:hover, .textarea:hover, .select:hover {
    border: 1px solid rgba(251, 251, 254, 0.8);
}
.input:focus, .input:active, .textarea:focus, .textarea:active, .select:focus, .select:active {
    border: 1px solid var(--fon);
}

.input-wrap {
    position: relative;
    & .label {
        display: block;
        font-weight: 400;
        font-size: 16px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-svetlyy);
        margin-bottom: 14px;
    }
}

.input-error {
    display: none;
    position: absolute;
    bottom: -3px;
    left: 15px;
    font-size: 11px;
    color: #ff4a37;
}
.input-clear {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    z-index: 5;
    cursor: pointer;
}

.textarea {
    outline: none;
    min-height: 142px;
    resize: none;
}
.select {
    cursor: pointer;
    outline: none;
    padding-right: 40px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: url(./../img/icons/arrow-bottom-white.svg) no-repeat right;
    background-position-x: calc(100% - 30px);
    & option {
        color: var(--shrift-temnyy);
    }
}

.politic {
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
    & a {
        color: inherit;
        font-weight: 500;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.5;
        }
    }
}



/*  search  */
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}

.form-search {
    position: relative;
    width: 100%;
    & input {
        background: var(--fon-plashek);
        opacity: 0.7;
        width: 100%;
        &:focus {
            opacity: 1;
        }
    }
    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}


/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotateX(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }

    &__btn-more {
        min-width: 10px;
    }
}

/* tabs */
.tab-btn {
    transition: all 0.3s ease;
}
.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}


/* rating */
.full-stars {
    text-align: center;
}
.full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.full-stars input {
    position: absolute;
    left: -9999px;
}
.full-stars label {
    margin: 0;
    cursor: pointer;
}
.full-stars label svg {
    margin: 0 2px;
    height: 18px;
    width: 20px;
    fill: var(--shrift-svetlyy);
    opacity: 1;
    -webkit-transition: fill 0.3s;
    -o-transition: fill 0.3s;
    transition: fill 0.3s;
}
.full-stars input:checked ~ label svg {
    opacity: 0.2;
}
.full-stars .rating-group:hover label svg {
    fill: var(--shrift-svetlyy);
    opacity: 1;
}
.full-stars .rating-group input:hover ~ label svg {
    opacity: 0.2;
}


/* breadcrumbs */
.breadcrumbs{
    margin-top: 20px;
    margin-bottom: 100px;
}
.breadcrumbs, .breadcrumbs li {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 8px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.breadcrumbs li, .breadcrumbs li a {
    font-size: 16px;
    color: var(--shrift-temnyy);
}
.breadcrumbs li a {
    color: var(--shrift-tekst-na-fone);
    transition: all 0.2s ease-in;
}
.breadcrumbs li a:hover {
    color: var(--shrift-temnyy);
}
.breadcrumbs li:after {
    content: "—";
    display: block;
    font-size: 18px;
    font-weight: 700;
    color: var(--osnovnoy-tusklyy);
}
.breadcrumbs li:last-child::after {
    content: none;
}


/*  media global  */
@media (max-width: 800px) {
    .section-offset {
        margin-bottom: 80px;
    }
}
@media (max-width: 768px) {
    body {
        font-size: 16px;
    }
    .section__top {
        margin-bottom: 35px;
        &_title {
            margin-bottom: 28px;
        }
    }
    .title {
        font-size: 32px;
    }
    .title-litle {
        font-size: 16px;
        margin-bottom: 30px;
    }
    .title-ellipse {
        font-size: 20px;
        min-height: 80px;
        margin-bottom: 15px;
        &.long span::before {
            content: "\A";
            white-space: pre;
        }
        & svg {
            min-width: calc(100% + 100px);
            min-height: 100%;
        }
        &__svg {
            display: none;
        }
        &__svg-mob {
            display: block;
        }
    }

    .primary-btn, .secondary-btn, .tertiary-btn, .form-btn-green, .form-btn-white {
        padding: 18px 25px 22px;
        font-size: 16px;
    }

    .btn-more {
        &::after {
            width: 8px;
            height: 6px;
        }
    }

    .input, .textarea, .select {
        padding: 16px;
    }
    .select {
        padding-right: 26px;
        background-position-x: calc(100% - 16px);
    }


    .sort {
        &__text {
            margin-bottom: 14px;
        }
        &__item {
            margin-bottom: 14px;
            & input[type="checkbox"] {
                width: 22px;
                height: 22px;
                &::before {
                    top: 3px;
                    left: 7px;
                    width: 6px;
                    height: 10px;
                    border-width: 0 2px 2px 0;
                }
            }
            & label {
                font-size: 16px;
            }
        }
    }

    .breadcrumbs{
        margin-top: 12px;
        margin-bottom: 18px;
    }
    .breadcrumbs, .breadcrumbs li {
        gap: 4px;
    }
    .breadcrumbs li, .breadcrumbs li a {
        font-size: 14px;
    }
}


