
// guarantees
.guarantees {
    padding: 150px 0;
    background: var(--osnovnoy-tusklyy);


    &__row {
        display: flex;
        justify-content: space-between;
        gap: 50px;
    }
    
    &__name {
        font-style: italic;
        font-weight: 500;
        font-size: 32px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-svetlyy);
        padding-left: 50px;
        margin-bottom: 28px;
    }


    &__text {
        flex-basis: 573px;
    }

    &__picture {
        width: fit-content;
        & img {
            max-width: 880px;
        }
    }

    &__list {
        & ul {
            padding-left: 50px;
            margin-bottom: 24px;
            display: flex;
            flex-direction: column;
            gap: 4px;
            & li {
                border-radius: 5px;
                padding: 27px 24px;
                background: rgba(251, 251, 254, 0.1);
                display: flex;
                align-items: center;
                gap: 20px;

                & picture {
                    width: 50px;
                    height: 50px;
                    & img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                
                & p {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 120%;
                    color: var(--shrift-svetlyy);
                    margin-bottom: 8px;
                }

                & span {
                    display: block;
                    font-style: italic;
                    color: #dfdff2;
                }
            }
        }
    }
}

// media guarantees
@media (max-width: 1100px) {
    .guarantees {
        &__row {
            display: block;
        }
        &__text {
            flex-basis: auto;
        }
        &__picture {
            display: none;
        }
    }
}
@media (max-width: 768px) {
    .guarantees {
        padding: 70px 0;
        &__name {
            font-size: 18px;
            padding-left: 44px;
            margin-bottom: 24px;
        }
        &__list {
            & ul {
                padding-left: 44px;
                & li {
                    border-radius: 5px;
                    padding: 16px;
                    flex-direction: column;
                    align-items: stretch;
                    gap: 20px;
                    & picture {
                        width: 36px;
                        height: 36px;
                    }
                    & p {
                        font-size: 16px;
                    }
                }
            }
        }
    }
}