
// footer
.footer {
    background: var(--osnovnoy-tusklyy);
    overflow: hidden;

    &__row {
        display: flex;
        margin-bottom: 75px;
    }

    &__block {
        padding: 75px 0;
        flex: 1 0 50%;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        padding-right: 60px;
        border-right: 1px solid rgba(251, 251, 254, 0.15);
    }

    &__text {
        display: flex;
        flex-direction: column;
        gap: 14px;
        font-style: italic;
        font-size: 16px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: #d0d0f2;
        max-width: 573px;

        & a {
            color: var(--shrift-svetlyy);
            &:hover {
                text-decoration: underline;
            }
        }
    }


    &__menu {
        padding-left: 80px;
        display: flex;
        justify-content: space-around;
        gap: 40px;
    }

    &__nav {
        & ul {
            display: flex;
            flex-direction: column;
            gap: 28px;
        }

        & a {
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            color: var(--shrift-svetlyy);
        }
    }

    &__nav-servisces {
        & p {
            font-weight: 400;
            font-size: 20px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: #dfdff2;
            margin-bottom: 24px;
            pointer-events: none;
        }
        & ul {
            gap: 20px;
        }
    }


    &__bottom {
        border-top: 1px solid rgba(251, 251, 254, 0.15);
        padding-top: 88px;
        & svg {
            width: 100%;
            overflow: visible;
        }
        // & svg path {
        //     transition: all 0.5s ease;
        //     &:hover {
        //         transform: translate(0, -30%);
        //     }
        // }
    }
}


// media footer
@media (max-width: 1024px) {
    .footer {
        display: flex;
        flex-direction: column;
        padding: 70px 10px 40px;
        &__row, &__container {
            display: contents;
        }
        &__block {
            padding: 0;
        }
        &__info {
            display: contents;
        }
        &__logo {
            margin-bottom: 30px;
        }
        &__text {
            order: 4;
            max-width: none;
        }
        &__menu {
            order: 2;
            justify-content: space-between;
            margin-bottom: 40px;
        }
        &__bottom {
            order: 3;
            border-top: none;
            padding-top: 0;
            margin-bottom: 60px;
            display: flex;
            justify-content: center;
            & svg {
                width: 80%;
            }
        }
    }
}

@media (max-width: 768px) {
    .footer {
        &__text {
            gap: 12px;
            font-size: 14px;
        }

        &__menu {
            flex-direction: column-reverse;
            gap: 24px;
        }
    
        &__nav {
            & ul {
                gap: 24px;
            }
            & a {
                font-size: 20px;
            }
        }
    
        &__nav-servisces {
            & p {
                font-weight: 500;
                font-size: 20px;
                line-height: 105%;
                letter-spacing: -0.02em;
                color: var(--shrift-svetlyy);
                margin-bottom: 0;
                display: flex;
                align-items: center;
                gap: 12px;
                pointer-events: all;
                &::after {
                    content: "";
                    display: block;
                    width: 9px;
                    height: 7px;
                    background: url("./../img/icons/arrow-bottom-white.svg") no-repeat center / contain;
                    transition: all 0.2s ease;
                }
                &.accordion--active {
                    &::after {
                        transform: rotateX(180deg);
                    }
                }
            }

            & ul {
                gap: 10px;
                &.accordion__content--active {
                    margin-top: 16px;
                }
            }

            & a {
                font-style: italic;
                font-weight: 400;
                font-size: 16px;
                line-height: 105%;
                letter-spacing: -0.02em;
                color: #dfdff2;
            }
        }

        &__bottom {
            margin: 0 -10px 32px;
            & svg {
                width: 100%;
            }
        }
    }
}