
// intro-service
.intro-service {
    background: var(--fon-blokov);
    position: relative;
    display: flex;
    overflow: hidden;

    &__content {
        flex: 0 0 100%;
    }

    &__body {
        width: 50%;
        padding: 0px 80px 100px 0;

        &_top {
            margin-bottom: 80px;
        }
    }

    & .breadcrumbs {
        margin-bottom: 30px;
    }

    &__title {
        margin-bottom: 32px;
    }

    &__cost {
        color: var(--shrift-temnyy);
        line-height: 105%;
        letter-spacing: -0.02em;
        width: fit-content;
        position: relative;
        & span {
            position: relative;
            z-index: 2;
        }
        & i {
            font-style: italic;
            font-weight: 500;
            font-size: 28px;
        }

        &::after {
            content: "";
            display: block;
            width: 100%;
            height: 6px;
            background: var(--akcentnyy-zelenyy);
            position: absolute;
            bottom: 2px;
            left: 0;
        }
    }

    &__list {
        margin-bottom: 40px;
        & ul {
            & li {
                display: block;
                font-style: italic;
                font-size: 20px;
                line-height: 120%;
                padding-left: 18px;
                position: relative;
                & span {
                    color: var(--shrift-temnyy);
                }

                &:not(:last-child) {
                    padding-bottom: 20px;
                    border-bottom: 1px solid #e6e6eb;
                    margin-bottom: 16px;
                }
            
                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star.svg") no-repeat center / contain;
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }
    }

    &__stock {
        position: relative;
        flex: 0 0 50%;
        transform: translate(-100%, 0);
        z-index: 2;
        box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
        background: var(--osnovnoy-tusklyy);

        &_bg {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            min-height: 71px;
            object-fit: cover;
        }

        &_wrap {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            gap: 32px;
        }
        
        &_number {
            font-weight: 500;
            font-size: 36px;
            color: var(--osnovnoy-tusklyy);
            width: 180px;
            height: 180px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: url("./../img/icons/star-3-white.svg") no-repeat center / contain;
        }
        
        &_text {
            font-weight: 500;
            font-size: 36px;
            line-height: 105%;
            text-align: center;
            color: #fff;
            max-width: 500px;

            & span {
                position: relative;
                z-index: 2;
                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 6px;
                    background: var(--akcentnyy-zheltyy);
                    position: absolute;
                    bottom: 2px;
                    left: 0;
                    z-index: -1;
                }
            }
        }

        &_btn {
            position: absolute;
            top: 50px;
            right: 50px;
            z-index: 3;
            border-radius: 5px;
            background: var(--fon);
            width: 48px;
            height: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }



}

// media intro-service
@media (max-width: 1200px) {
    .intro-service {
        flex-direction: column;
        gap: 22px;
        &__body {
            width: 100%;
            padding: 0;
        }
        &__stock {
            flex: 0 0 100%;
            transform: translate(0, 0);
            border-radius: 5px;
            padding: 24px;
            margin: 10px;
            min-height: 180px;
            overflow: hidden;
            &_wrap {
                align-items: start;
                justify-content: start;
            }
            &_number {
                position: absolute;
                bottom: -45px;
                right: -40px;
                transform: rotate(15deg);
            }
            
            &_text {
                text-align: left;
                max-width: 500px;
            }
            &_btn {
                display: none;
            }
        }
    }
}
@media (max-width: 768px) {
    .intro-service {
        &__body {
            &_top {
                margin-bottom: 36px;
            }
        }
        & .breadcrumbs {
            margin-bottom: 28px;
        }
        &__title {
            margin-bottom: 16px;
        }
        &__subtitle {
            font-style: italic;
            margin-bottom: 16px;
        }
        &__cost {
            & i {
                font-size: 24px;
            }
        }
    
        &__list {
            margin-bottom: 20px;
            & ul {
                & li {
                    font-size: 16px;
                    &:not(:last-child) {
                        padding-bottom: 12px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    
        &__stock {
            padding: 16px;
            &_bg {
                min-height: 45px;
            }
            
            &_number {
                font-size: 36px;
                width: 130px;
                height: 130px;
                bottom: -35px;
                right: -25px;
            }
            &_text {
                font-size: 24px;
                max-width: 60%;
            }
        }
    }
}