
// popup-culc
.popup-culc {
    &__content {
        border-radius: 5px;
        padding: 60px;
        max-width: 840px;
        background: var(--akcentnyy-zelenyy);
        color: #dfdff2;
        position: relative;
        overflow: hidden;
    }
 
    &__title {
        color: var(--shrift-svetlyy);
        margin-bottom: 48px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 24px;
        margin-bottom: 12px;
    }

    &__result {
        border-radius: 5px;
        padding: 24px 32px;
        background: var(--fon);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-top: 4px;

        &_item {
            font-weight: 400;
            font-size: 16px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-tekst-na-fone);

        }
        
        &_top {
            display: flex;
            gap: 12px;
            margin-bottom: 6px;
            font-weight: 500;
            font-size: 32px;
            line-height: 105%;
            color: var(--shrift-temnyy);
            font-style: italic;
        }
    }
}

// media popup-culc
@media (max-width: 768px) {
    .popup-culc {
        &__content {
            padding: 40px 16px;
        }
        &__title {
            margin-bottom: 30px;
        }
        &__form {
            gap: 20px;
        }
        &__result {
            padding: 16px;
            &_item {
                font-size: 14px;
            }
            &_top {
                gap: 6px;
                font-size: 18px;
                & img {
                    width: 16px;
                    height: 16px;
                    object-fit: contain;
                }
            }
            &_icon {
                display: none;
            }
        }
    }
}
