//intro 
.intro {
    padding: 80px 0;
    background: var(--fon-blokov);

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
        margin-bottom: 60px;
    }

    &__title {
        flex-basis: 620px;
    }

    &__advantages {
        flex-basis: 573px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 24px;

        & ul {
            & li {
                padding-left: 18px;
                position: relative;

                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star.svg") center / contain;
                    position: absolute;
                    top: 5px;
                    left: 0;
                }
                
                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid #e6e6eb;
                    margin-bottom: 12px;
                }

                & span {
                    color: var(--shrift-temnyy);
                }
            }
        }
    }

    &__picture {
        & img {
            max-height: 600px;
        }
    }
}


// madia intro
@media (max-width: 1024px) {
    .intro {
        &__row {
            flex-direction: column;
        }
        &__title {
            flex-basis: auto;
        }
        &__advantages {
            flex-basis: auto;
        }
    }
}

@media (max-width: 768px) {
    .intro {
        padding: 44px 0 0;
        &__row {
            gap: 24px;
            margin-bottom: 16px;
        }
        &__advantages {
            gap: 20px;
    
            & ul {
                & li {
                    padding-left: 12px;
                    &::before {
                        width: 8px;
                        height: 8px;
                    }
                    &:not(:last-child) {
                        padding-bottom: 12px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
        &__picture {
            width: calc(100% + 20px);
            margin-left: -10px;
            margin-right: -10px;
            & img {
                max-height: 300px;
                min-height: 220px;
                border-radius: 0;
            }
        }
    }
}