.popup__search {
    &::-webkit-scrollbar {
        width: 4px;
        background-color: var(--izobrazhenie);
        padding: 5px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: var(--color-zelenyy);
    }
}

.popup__search-result {
    margin-top: 0;
    max-height: 370px;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 4px;
        background-color: rgba(251, 251, 254, 0.15);
        padding: 5px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: var(--fon);
    }
}

.no-results-message {
    font-style: italic;
    line-height: 105%;
    letter-spacing: -0.02em;
    color: #dfdff2;
}

.none {
    display: none;
}


@media (max-width: 768px) {
    .close-popup {
        top: 16px;
        right: 16px;
        width: 14px;
        height: 14px;
    }
}