
// stages
.stages {
    padding: 150px 0;
    background: var(--osnovnoy-tusklyy);
    overflow: hidden;

    &__tab {
        display: flex;
        justify-content: space-between;
        gap: 70px;
    }

    &__btns {
        display: flex;
        flex-direction: column;
        gap: 6px;
        list-style-type: none;
        counter-reset: item;
        flex: 1 0 32.5%;
        max-width: 573px;
        height: fit-content;
    }

    &__btn {
        border-radius: 5px;
        padding: 22px 26px 26px;
        background: rgba(251, 251, 254, 0.15);
        font-weight: 500;
        font-size: 24px;
        line-height: 120%;
        color: var(--shrift-svetlyy);
        display: flex;
        align-items: center;
        gap: 11px;
        width: 100%;

        &::before {
            content: counter(item, decimal-leading-zero); 
            counter-increment: item;
        }

        &.active {
            background: var(--fon);
            color: var(--shrift-temnyy);
            &::before {
                color: var(--osnovnoy-tusklyy);
            }
        }
    }

    & .tab-content {
        display: block;
        position: absolute;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
    }
    
    & .tab-content.active {
        position: relative;
        visibility: visible;
        opacity: 1;
        pointer-events: auto;
    }

    &__contents {
        list-style-type: none;
        counter-reset: itemc;
        flex: 1 0 56%;
        max-width: 980px;
    }

    &__content {
        padding: 8px 0;
        line-height: 120%;
        color: #dfdff2;
        font-style: italic;
        padding-left: 100px;
        border-left: 1px solid rgba(251,251,254,0.15);
        position: relative;
        
        &::before {
            content: counter(itemc, decimal-leading-zero); 
            counter-increment: itemc;
            font-weight: 500;
            font-size: 54px;
            line-height: 120%;
            color: var(--shrift-svetlyy);
            font-style: italic;
            opacity: 0.4;
            position: absolute;
            top: 0;
            left: 24px;
        }

        &_wrap {
            min-height: 360px;
        }

        &_title {
            font-weight: 500;
            font-size: 28px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            margin-bottom: 28px;
        }

        & p:not(.stages__content_title) {
            margin-bottom: 20px;
        }

        & ul {
            display: flex;
            flex-direction: column;
            gap: 12px;
            &:not(:last-child) {
                margin-bottom: 20px;
            }

            & li {
                color: var(--shrift-svetlyy);
                padding-left: 18px;
                position: relative;

                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                }

                &::before {
                    content: ""; 
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }

        &_btn-more {
            margin-top: -21px;
            &.show {
                margin-top: 25px;
            }
        }
    }
}

// media stages
@media (max-width: 1024px) {
    .stages {
        &__tab {
            flex-direction: column;
            gap: 24px;
        }
        &__btns {
            flex-direction: row;
            flex: 0 0 auto;
            overflow-x: auto;
            max-width: none;
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }

        &__contents {
            flex: 0 0 auto;
            max-width: none;
        }
    
        &__content {
            padding: 29px 0 0 100px;
            border-left: none;
            border-top: 1px solid rgba(251,251,254,0.15);

            &::before {
                top: 16px;
                left: 6px;
            }
        }
    }
}
@media (max-width: 768px) {
    .stages {
        padding: 70px 0;
        &__btn {
            padding: 14px;
            font-size: 14px;
            gap: 6px;
        }
        &__content {
            padding: 29px 0 0 44px;
            &::before {
                left: -37px;
            }
            &_title {
                font-size: 20px;
                margin-bottom: 16px;
            }
            & p:not(.stages__content_title) {
                margin-bottom: 12px;
            }
            & ul {
                gap: 10px;
                &:not(:last-child) {
                    margin-bottom: 12px;
                }
                & li {
                    padding-left: 14px;
                    &:not(:last-child) {
                        padding-bottom: 14px;
                    }
                    &::before {
                        width: 8px;
                        height: 8px;
                    }
                }
            }
        }
    }
}