
// services

.services {
    &__title {
        max-width: 884px;
        margin-bottom: 80px;
    }

    &__item {
        border-bottom: 1px solid var(--seryy);
        &:first-child {
            border-top: 1px solid var(--seryy);
        }

        &_row {
            padding: 32px 0;
            display: flex;
            justify-content: space-between;
            gap: 20px;
            text-align: left;
        }

        &_name {
            flex: 1 0 40%;
            font-weight: 500;
            font-size: 28px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
        }

        &_info {
            flex: 1 0 40%;
            font-size: 16px;
            font-style: italic;
            letter-spacing: -0.02em;
            color: var(--shrift-tekst-na-fone);
        }

        &_btn {
            display: flex;
            align-items: center;
            transition: all 0.2s ease-in;
        }
    }

    &__tab {
        margin-bottom: 10px;
    }

    &__table {
        width: 100%;

        &_body {
            display: flex;
            flex-direction: column;
            gap: 10px;
            width: 100%;
        }

        &_item {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            border-radius: 5px;
            padding: 20px;
            background: var(--fon-blokov);
        }

        &_name {
            flex: 1 0 45%;
            font-weight: 400;
            font-size: 20px;
            line-height: 110%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
        }

        &_price {
            flex: 1 0 30%;
            font-weight: 500;
            font-size: 20px;
            line-height: 120%;
            color: var(--akcentnyy-zelenyy);
        }

        &_btn button {
            padding: 10px 25px;
        }
    }

    &__tab-btns {
        display: flex;
        gap: 8px;
        overflow-x: auto;
        margin-bottom: 24px;

        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }

    &__tab-btn {
        border: 1px solid rgba(172, 173, 223, 0.3);
        border-radius: 5px;
        padding: 12px 16px 12px 16px;
        font-size: 16px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--osnovnoy-tusklyy);
        text-wrap: nowrap;
        transition: all 0.3s ease;
        &:hover {
            border: 1px solid var(--osnovnoy-tusklyy);
        }
        &.active {
            border: 1px solid var(--osnovnoy-tusklyy);
            background: var(--osnovnoy-tusklyy);
            color: var(--shrift-svetlyy);
        }
    }

    &__content_btn {
        margin-bottom: 40px;
    }
}


// media services
@media (max-width: 768px) {
    .services {
        &__title {
            margin-bottom: 30px;
        }
    
        &__item {
            &_row {
                padding: 24px 0;
                flex-wrap: wrap;
                gap: 8px 20px;
            }
            &_name {
                flex: 1 0 70%;
                font-size: 18px;
            }
            &_info {
                flex: 1 0 100%;
                font-size: 14px;
                display: flex;
                gap: 8px;
                order: -1;
                & p:first-child::after {
                    content: "|";
                    padding-left: 8px;
                }
            }
        }
    
        &__tab {
            margin-bottom: 12px;
        }
    
        &__table {
            &_item {
                padding: 16px;
                flex-wrap: wrap;
                gap: 14px;
            }
            &_name {
                flex: 1 0 100%;
                font-size: 16px;
            }
            &_price {
                flex: 1 0 45%;
                font-size: 18px;
            }
            &_btn button {
                padding: 6px 18px;
            }
        }
    
        &__tab-btns {
            gap: 5px;
            margin-bottom: 20px;
        }
        &__tab-btn {
            padding: 10px 12px;
            font-size: 14px;
        }

        &__content_btn {
            margin-bottom: 24px;
        }
    }
}