
// jobs
.jobs {
    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 5px;
        background: var(--osnovnoy-tusklyy);
        height: fit-content;
        color: var(--shrift-svetlyy);
        padding-bottom: 2px;

        &_top {
            padding: 24px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_name {
            font-weight: 500;
            font-size: 22px;
            line-height: 105%;
        }

        &_content {
            padding: 20px 24px 24px;
        }

        &_texts {
            display: flex;
            flex-direction: column;
            gap: 24px;
        }

        &_text {
            span {
                display: block;
                font-size: 16px;
                line-height: 120%;
                color: #dfdff2;
                margin-bottom: 8px;
            }
        }

        &_list {
            & ul {
                display: flex;
                flex-wrap: wrap;
                gap: 12px 60px;
                margin-bottom: 28px;
                & li {
                    flex: 1 0 40%;
                    line-height: 120%;
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    position: relative;
                    padding-left: 18px;
                    &::before {
                        content: "";
                        display: block;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                        position: absolute;
                        top: 5px;
                        left: 0;
                    }
                }
            }

        }

        &_connect {
            border: 2px solid var(--osnovnoy-tusklyy);
            border-radius: 5px;
            padding: 24px;
            background: var(--fon);
            color: var(--shrift-temnyy);
            & p {
                font-weight: 500;
                font-size: 26px;
                margin-bottom: 20px;
            }
        }

        &_info {
            & ul {
                display: flex;
                flex-wrap: wrap;
                gap: 12px 60px;
                & li {
                    flex: 1 0 40%;
                    line-height: 120%;

                    & span  {
                        display: block;
                        font-style: italic;
                        font-size: 16px;
                        line-height: 120%;
                        color: var(--shrift-tekst-na-fone);
                        position: relative;
                        padding-left: 18px;
                        margin-bottom: 8px;
                        &::before {
                            content: "";
                            display: block;
                            width: 10px;
                            height: 10px;
                            background: url("./../img/icons/star.svg") no-repeat center / contain;
                            position: absolute;
                            top: 5px;
                            left: 0;
                        }
                    }

                    & a {
                        font-weight: 500;
                        color: var(--shrift-temnyy);
                    }
                }
            }

        }
    }

}

// media jobs
@media (max-width: 1024px) {
    .jobs {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
    }
}

@media (max-width: 768px) {
    .jobs {
        &__card {
            &_top {
                padding: 16px;
            }
            &_name {
                font-size: 18px;
            }
            &_content {
                padding: 14px 16px 24px 44px;
            }
            &_texts {
                gap: 20px;
            }
            &_text {
                span {
                    font-size: 14px;
                    margin-bottom: 6px;
                }
            }
            &_list {
                & ul {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 10px;
                    & li {
                        flex: 1 0 100%;
                        padding-bottom: 14px;
                        padding-left: 14px;
                        &::before {
                            width: 8px;
                            height: 8px;
                        }
                    }
                }
            }
    
            &_connect {
                padding: 20px 16px;
                & p {
                    font-size: 20px;
                    margin-bottom: 24px;
                }
            }
    
            &_info {
                & ul {
                    gap: 10px;
                    & li {
                        flex: 1 0 100%;
                        &:not(:last-child) {
                            padding-bottom: 14px;
                            border-bottom: 1px solid #e6e6eb;
                        }
                        & span  {
                            font-size: 14px;
                            padding-left: 14px;
                            &::before {
                                width: 8px;
                                height: 8px;
                            }
                        }
                    }
                }
            }
        }
    }
}