// header
.header {
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 300;
    width: 100%;

    &__top {
        border-bottom: 1px solid var(--seryy);
        position: relative;
        transition: all 0.3s ease;
        
        &_wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
            padding: 26px 0;
        }

        &_block {
            flex-basis: 410px;
        }
    }

    &__location {
        font-size: 18px;
        line-height: 120%;
        letter-spacing: -0.02em;
        color: var(--shrift-tekst-na-fone);
        display: flex;
        align-items: center;
        gap: 13px;
        & button {
            color: var(--shrift-temnyy);
            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__nav {
        &_list {
            display: flex;
            align-items: center;
            gap: 32px;
            & a, & button {
                font-size: 18px;
                line-height: 120%;
                letter-spacing: -0.02em;
                color: var(--shrift-temnyy);
                transition: all 0.2s ease-in;
                
                &:hover {
                    color: var(--osnovnoy-tusklyy);
                }
            }
        }

        &_services {
            position: relative;
            & > button {
                display: flex;
                align-items: center;
                gap: 8px;
                & svg path {
                    transition: all 0.2s ease-in;
                }
                &:hover svg path {
                    fill: var(--osnovnoy-tusklyy);
                }
            }
            &.active {
                & > button {
                    color: var(--osnovnoy-tusklyy);
                    & svg {
                        transform: rotate(180deg);
                        & path {
                            fill: var(--osnovnoy-tusklyy);
                        }
                    }
                }
                & .header__submenu {
                    display: block;
                }
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        position: relative;
        padding: 30px 0;
        border-bottom: 1px solid var(--seryy);
    }

    &__right {
        display: flex;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
    }

    &__icon {
        border-left: 1px solid var(--seryy);
        min-width: 84px;
        & a, & button {
            padding: 0 32px;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        & svg path {
            transition: all 0.3s ease;
        }

        &:hover {
            & svg:not(.ham) path {
                fill: #9293d8;
            }
        }
    }

    &__phone {
        & a {
            padding: 30px;
            border-left: 1px solid var(--seryy);
            height: 100%;
            min-width: 321px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 20px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            transition: all 0.2s ease-in;
            
            &:hover {
                color: var(--osnovnoy-tusklyy);
            }
        }
    }

    &__btn {
        padding: 30px;
        height: 100%;
        min-width: 321px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(114, 206, 198, 0.15);
        font-size: 20px;
        line-height: 120%;
        color: var(--akcentnyy-zelenyy);
        transition: all 0.2s ease-in;
            
        &:hover {
            background: var(--akcentnyy-zelenyy);
            color: var(--shrift-svetlyy);
        }

        &:active {
            background: rgba(114, 206, 198, 0.8);
        }
    }


    &__submenu {
        display: none;
        border-radius: 5px;
        padding: 4px;
        box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.08);
        background: var(--fon);
        position: absolute;
        top: calc(100% + 42px);
        left: 0;
        width: max-content;
        max-width: 600px;
        & a {
            display: block;
            padding: 10px 20px 14px 20px;
            border-radius: 5px;
            &:hover {
                color: var(--shrift-temnyy);
                background: rgba(172, 173, 223, 0.15);
            }
        }
    }

    &__mobile-wrap {
        display: none;

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid rgba(251, 251, 254, 0.15);
            margin: 0 -10px 32px;
            
            & .header__icon {
                border-right: 1px solid rgba(251, 251, 254, 0.15);
                border-left: 1px solid rgba(251, 251, 254, 0.15);
            }
        }
        
        &_list {
            display: flex;
            flex-direction: column;
            gap: 12px;
            margin-left: 44px;
            margin-bottom: 24px;
        }

        &_item {
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(251,251,254,0.15);
            
            & span {
                display: block;
                font-style: italic;
                color: #dfdff2;
                padding-left: 16px;
                position: relative;
                margin-bottom: 8px;

                &::before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 8px;
                    background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }

            & a, & p {
                font-weight: 500;
                font-size: 22px;
                line-height: 120%;
                letter-spacing: -0.02em;
                color: var(--shrift-svetlyy);
            }
        }

        &_network {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 60px;
            height: 60px;
            border-radius: 5px;
            border: 1px solid rgba(251, 251, 254, 0.2);
            margin-left: 44px;
        }
    }
}

.main {
    margin-top: 167px;
}

.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.hamRotate.active {
    transform: rotate(45deg);
}
.hamRotate180.active {
    transform: rotate(180deg);
}
.line {
    fill:none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: var(--osnovnoy-tusklyy);
    stroke-width: 3;
    stroke-linecap:round;
}
.ham1 .top {
    stroke-dasharray: 40 139;
}
.ham1 .bottom {
    stroke-dasharray: 40 180;
}
.ham1.active .top {
    //stroke-dashoffset: -98px;
    stroke-dashoffset: -96px;
}
.ham1.active .bottom {
    //stroke-dashoffset: -138px;
    stroke-dashoffset: -140px;
}

.ham-white .line {
    stroke: var(--fon);
}


// media header
@media (max-width: 1200px) {
    .header {
        background: #fff;

        & .container {
            padding: 0;
        }

        &__mobile-wrap {
            display: block;
        }
    
        &__top {
            &_wrap {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 30px;
                padding: 0;
            }
    
            &_block {
                display: none;
            }
        }
    
        &__location {
            display: none;
        }

        &__menu {
            display: none;

            display: block;
            padding: 0 10px 20px 10px;
            background: var(--osnovnoy-tusklyy);
            min-height: 100vh;
            width: 100%;
            position: fixed;
            top: 0;
            left: -100%;
            z-index: 101;
            pointer-events: none;
            transition: all 0.3s ease;
        }

        &__menu--active {
            display: block;
            position: fixed;
            top: 0px;
            left: 0;
            pointer-events: all;
        }
    
        &__nav {
            margin-bottom: 40px;
            margin-left: 44px;
            &_list {
                flex-direction: column;
                align-items: start;
                gap: 24px;
                & a, & button {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 105%;
                    letter-spacing: -0.02em;
                    color: var(--shrift-svetlyy);
                    &:hover {
                        color: var(--shrift-svetlyy);
                    }
                }
            }
    
            &_services {
                position: relative;
                & > button {
                    & svg path {
                        fill: var(--shrift-svetlyy);
                    }
                    &:hover svg path {
                        fill: var(--shrift-svetlyy);
                    }
                }
                &.active {
                    & > button {
                        color: var(--shrift-svetlyy);
                        & svg {
                            & path {
                                fill: var(--shrift-svetlyy);
                            }
                        }
                    }
                }
            }
        }
    
        &__right {
            display: none;
        }
    
        &__icon {
            border-left: 1px solid var(--seryy);
            border-right: 1px solid var(--seryy);
            min-width: 55px;
            & a, & button {
                min-height: 56px;
                padding: 0;
            }
        }

        &__submenu {
            display: none;
            border-radius: 0;
            padding: 0;
            box-shadow: none;
            background: transparent;
            position: static;
            width: 100%;
            max-width: none;
            & a {
                padding: 6px 20px;
                border-radius: 0;
                font-style: italic;
                font-weight: 400;
                font-size: 16px;
                line-height: 120%;
                color: #dfdff2;
                &:hover {
                    color: var(--shrift-svetlyy);
                    background: transparent;
                }
            }
        }
    }
    .main {
        margin-top: 57px;
    }
}