// text-block-1
.text-block {
    &__grid {
        display: grid;
        grid-template-columns: 6fr 3fr;
        gap: 40px;
        &:not(:last-child) {
            margin-bottom: 80px;
        }
    }

    &__right {
        height: fit-content;
    }
}


.text {
    font-style: italic;

    & h2, & h3, & h4, & h5, & h6 {
        font-style: normal;
        font-weight: 500;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-temnyy);
        margin-bottom: 40px;
    }
    & h2 {
        font-size: 52px;
        margin-bottom: 50px;
    }
    & h3 {
        font-size: 42px;
    }
    & h4 {
        font-size: 32px;
    }
    & h5 {
        font-size: 24px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin-bottom: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 28px;
        }
    }
    & span {
        font-weight: 500;
    }
    & a {
        font-weight: 500;
        text-decoration: underline;
        color: var(--shrift-tekst-na-fone);
        transition: all 0.3s ease;
        &:hover {
            color: var(--osnovnoy-tusklyy);
        }
    }

    
    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 28px;
        & li {
            padding-bottom: 20px;
            border-bottom: 1px solid var(--seryy);
            padding-left: 18px;
            position: relative;
            color: var(--shrift-temnyy);
            &:first-child {
                padding-top: 16px;
                border-top: 1px solid var(--seryy);
                &::before {
                    top: 20px;
                }
            }
            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/star.svg") no-repeat center / contain;
                position: absolute;
                top: 4px;
                left: 0;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 28px;
        list-style-type: none;
        counter-reset: item;
        & li {
            display: flex;
            gap: 29px;
            padding-bottom: 20px;
            border-bottom: 1px solid var(--seryy);
            color: var(--shrift-temnyy);
            &:first-child {
                padding-top: 16px;
                border-top: 1px solid var(--seryy);
            }
            &::before {
                content: counter(item, decimal-leading-zero); 
                counter-increment: item;
                display: block;
                font-weight: 500;
                font-size: 20px;
                line-height: 120%;
                color: var(--osnovnoy-tusklyy);
                font-style: italic;
            }
        }
    }


    & .picture {
        float: left;
        min-height: auto;
        width: 50%;
        margin-right: 20px;
    }


    & .blockquote {
        border-radius: 5px;
        padding: 32px 32px 32px 103px;
        background: var(--fon-blokov);
        position: relative;
        margin-bottom: 28px;

        &__bg {
            position: absolute;
            top: 39px;
            left: 32px;
            display: block;
            width: 52px;
            height: 44px;
            object-fit: contain;
        }
        
        &__main {
            padding-bottom: 44px;
            border-bottom: 1px solid #e6e6eb;
            margin-bottom: 20px;
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            & span {
                color: var(--osnovnoy-tusklyy);
            }
        }
        
        &__editor {
            display: flex;
            align-items: center;
            gap: 20px;

            &_img {
                display: block;
                border-radius: 40px;
                min-width: 70px;
                width: 70px;
                height: 70px;
                overflow: hidden;
                background: var(--osnovnoy-tusklyy);
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        
            &_text {
                & p {
                    font-style: italic;
                    font-weight: 500;
                    font-size: 22px;
                    line-height: 120%;
                    color: var(--shrift-temnyy);
                    margin-bottom: 4px !important;
                }
                & span {
                    font-weight: 400;
                }
            }
        }
    }
}

.navigation {
    border-radius: 5px;
    padding: 24px;
    background: var(--osnovnoy-tusklyy);
    height: fit-content;
    position: sticky;
    top: 30px;

    &__list {
        display: flex;
        flex-direction: column;
        gap: 16px;
        & > li {
            border-bottom: 1px solid rgba(251,251,254,0.15);
            position: relative;
            padding: 0 22px 20px;

            &::before {
                content: "*";
                font-size: 22px;
                color: var(--shrift-svetlyy);
                position: absolute;
                top: 6px;
                left: 0;
            }

            &::after {
                content: "";
                display: block;
                width: 12px;
                height: 9px;
                background: url("./../img/icons/arrow-bottom-white.svg") no-repeat center / contain;
                position: absolute;
                top: 6px;
                right: 0;
                transition: all 0.2s ease-in;
            }

            &.accordion--active {
                &::after {
                    transform: rotateX(180deg);
                }
            }

            & > a {
                font-size: 22px;
                line-height: 120%;
                color: var(--shrift-svetlyy);
            }
        }
    }

    &__sublist {
        display: flex;
        flex-direction: column;
        gap: 14px;
        &.accordion__content--active {
            margin-top: 20px;
        }
        & > li > a {
            font-style: italic;
            line-height: 105%;
            color: #dfdff2;
            transition: all 0.2s ease-in;
            &:hover {
                color: var(--shrift-svetlyy);
            }
        }
    }
}

.author {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: fit-content;
    position: sticky;
    top: 30px;

    &__doc {
        display: block;
        border: 1px solid var(--osnovnoy-tusklyy);
        border-radius: 5px;
        padding: 24px;
        font-style: italic;
        color: var(--shrift-tekst-na-fone);

        &_text {
            font-size: 16px;
            padding-left: 18px;
            position: relative;
            margin-bottom: 24px;
            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/star.svg") no-repeat center / contain;
                position: absolute;
                top: 4px;
                left: 0;
            }
        }

        &_who {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &_img {
            display: block;
            border-radius: 40px;
            min-width: 70px;
            width: 70px;
            height: 70px;
            overflow: hidden;
            background: var(--osnovnoy-tusklyy);
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_fio {
            & p {
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 120%;
                color: var(--shrift-temnyy);
                margin-bottom: 4px;
            }
        }

        &_date {
            display: flex;
            align-content: center;
            justify-content: space-between;
            gap: 20px;
            font-size: 16px;
            padding-top: 10px;
            border-top: 1px solid var(--seryy);
            margin-top: 16px;
        }
    }

}

.article {
    &__top {
        display: flex;
        gap: 120px;
        margin-bottom: 28px;
        &_text {
            color: var(--shrift-temnyy);
            position: relative;
            padding-left: 30px;
            & span {
                color: var(--shrift-tekst-na-fone);
            }
            &::before {
                content: "";
                display: block;
                width: 22px;
                height: 22px;
                position: absolute;
                top: -2px;
                left: 0;
            }
            &.eye::before {
                background: url("./../img/icons/eye-purple.svg") no-repeat center / contain;
            }
            &.time::before {
                background: url("./../img/icons/time-purple.svg") no-repeat center / contain;
            }
        }
    }

    &__docs {
        flex-direction: row;
        gap: 10px;
        margin-bottom: 32px;
        position: static;
        & .author__doc {
            flex: 1 0 45%;
        }
    }

    &__literature {
        &_title {
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            margin-bottom: 24px;
        }
        & ul:not(.breadcrumbs) {
            margin-bottom: 24px;
            & li {
                color: var(--shrift-tekst-na-fone);
                &::before {
                    background: url("./../img/icons/star-green.svg") no-repeat center / contain;
                }
            }
        }
    }

    &__bottom {
        display: flex;
        gap: 10px;

        &_item {
            flex: 1 0 45%;
            border-radius: 5px;
            padding: 12px;
            background: var(--osnovnoy-tusklyy);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            font-style: italic;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--shrift-svetlyy);
        }

        & label {
            max-height: 20px;
        }

        &_networks {
            display: flex;
            gap: 10px;
        }

        &_network {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            background: rgba(251, 251, 254, 0.1);
            min-width: 44px;
            width: 44px;
            height: 44px;
            transition: all 0.3s ease;
            & svg path {
                transition: all 0.3s ease;
            }
            &:hover {
                background: var(--fon);
                & svg path {
                    fill: var(--osnovnoy-tusklyy);
                }
            }
        }
    }
}


// media text-block
@media (max-width: 1024px) {
    .text-block {
        &__grid {
            grid-template-columns: 1fr;
            gap: 40px;
        }
        &__text {
            order: 2;
        }
        & .picture img {
            max-height: 450px;
            object-position: top;
        }
    }
    .navigation {
        position: static;
    }
    .author {
        order: 3;
        position: static;
    }
}
@media (max-width: 768px) {
    .text-block {
        &__grid {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
    }
    .text {
        & h2 {
            font-size: 30px;
            margin-bottom: 30px;
        }
        & h3 {
            font-size: 24px;
            margin-bottom: 24px;
        }
        & h4 {
            font-size: 20px;
            margin-bottom: 20px;
        }
        & h5 {
            font-size: 16px;
            margin-bottom: 20px;
        }
        & p:not(:last-child) {
            margin-bottom: 20px;
        }

        & ul:not(.breadcrumbs) {
            gap: 10px;
            margin-bottom: 20px;
            & li {
                padding-bottom: 14px;
                padding-left: 14px;
                &:first-child {
                    padding-top: 14px;
                    &::before {
                        top: 18px;
                    }
                }
                &::before {
                    width: 8px;
                    height: 8px;
                }
            }
        }

        & ol {
            gap: 10px;
            margin-bottom: 20px;
            & li {
                gap: 25px;
                padding-bottom: 14px;
                &:first-child {
                    padding-top: 14px;
                }
                &::before {
                    font-size: 18px;
                }
            }
        }

        & .picture {
            float: none;
            width: 100%;
            margin-right: 0;
            margin-bottom: 20px;
            & img {
                min-height: 160px;
                max-height: 340px;
            }
        }

        & .blockquote {
            padding: 16px 16px 16px 44px;
            margin-bottom: 20px;
            &__bg {
                top: 22px;
                left: 11px;
                width: 21px;
                height: 18px;
            }
            &__main {
                padding-bottom: 16px;
                margin-bottom: 16px;
                font-size: 18px;
            }
            &__editor {
                gap: 10px;
                &_img {
                    min-width: 44px;
                    width: 44px;
                    height: 44px;
                }
                &_text {
                    & p {
                        font-size: 16px;
                    }
                }
            }
        }
    }

    .navigation {
        padding: 24px 16px;
        &__list {
            gap: 10px;
            & > li {
                padding: 0 28px 14px 20px;
                &::before {
                    font-size: 18px;
                }
                &::after {
                    width: 8px;
                    height: 6px;
                }
                & > a {
                    font-size: 18px;
                }
            }
        }
    
        &__sublist {
            gap: 10px;
            &.accordion__content--active {
                margin-top: 12px;
            }
        }
    }

    .author {
        &__doc {
            padding: 16px;
            &_who {
                gap: 10px;
            }
            &_img {
                min-width: 44px;
                width: 44px;
                height: 44px;
            }
            &_fio {
                & p {
                    font-size: 16px;
                }
                & span {
                    font-size: 14px;
                }
            }
            &_date {
                font-size: 14px;
                padding-top: 6px;
            }
        }
    }

    .article {
        &__top {
            gap: 50px;
            margin-bottom: 13px;
            &_text {
                padding-left: 26px;
                & span {
                    display: none;
                }
                &::before {
                    width: 18px;
                    height: 18px;
                    top: 0px;
                }
            }
        }
        &__docs {
            flex-direction: column;
        }
        &__literature {
            &_title {
                font-size: 22px;
                margin-bottom: 20px;
            }
            & ul:not(.breadcrumbs) {
                margin-bottom: 30px;
            }
        }
        &__bottom {
            flex-direction: column;
            gap: 4px;
            &_item {
                padding: 10px 16px;
                font-size: 14px;
            }
            &_networks {
                gap: 4px;
            }
            &_network {
                min-width: 32px;
                width: 32px;
                height: 32px;
            }
        }
    }
    
}
@media (max-width: 400px) {
    .text-block {
        & .picture img {
            max-height: 200px;
        }
    }
}