@charset "UTF-8";
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --container-width: 1780px;
  --container-padding: 10px;
  --font-family: "Cygre", sans-serif;
  --plashki: #424e59;
  --izobrazhenie: #d2dce8;
  --fon-plashek: #eaedf2;
  --temnyy: #080b0f;
  --tekst: #003649;
  --tekst-svetlyy: #405f6a;
  --belyy: #fff;
  --tegi: #556473;
  --bg-svetlo-seryy: #f9f9f9;
  --color-temno-zelenyy: #206175;
  --color-zelenyy: #3d7c85;
  --color-svetlo-zelenyy-1: #bedbd7;
  --color-svetlo-zelenyy-2: rgba(190, 219, 215, 0.4);
  --gradient-green: linear-gradient(222deg, rgba(106,165,174,1) 0%, rgba(61,124,133,1) 100%);
  --gradient-light-gray: linear-gradient(222deg, rgba(247,250,255,1) 0%, rgba(234,237,242,1) 100%);
  --gradient-for-photo-decor: linear-gradient(0deg, rgba(32,97,117,1) 0%, rgba(32,97,117,0) 30%, rgba(32,97,117,0) 100%);
  --decor-gray: #C7D6DC;
  --osnovnoy-tusklyy: #acaddf;
  --akcentnyy-zelenyy: #72cec6;
  --fon-blokov: #f4f6fa;
  --seryy: #f1f1f3;
  --fon: #fbfbfe;
  --shrift-temnyy: #2e3749;
  --shrift-tekst-na-fone: #a4a6af;
  --shrift-svetlyy: #fbfbfe;
  --shrift-svetlyy-tekst: #eeeffa;
  --osnovnoy-yarkiy: #c09fe6;
  --akcentnyy-krasnyy: #cd7f84;
  --akcentnyy-zheltyy: #ffd471;
}

/* Cygre */
@font-face {
  font-family: "Cygre";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local("Cygre"), url("../fonts/Cygre-Regular.woff2") format("woff2"), url("../fonts/Cygre-Regular.woff") format("woff");
}
@font-face {
  font-family: "Cygre";
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src: local("Cygre"), url("../fonts/Cygre-RegularIt.woff2") format("woff2"), url("../fonts/Cygre-RegularIt.woff") format("woff");
}
@font-face {
  font-family: "Cygre";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: local("Cygre"), url("../fonts/Cygre-Medium.woff2") format("woff2"), url("../fonts/Cygre-Medium.woff") format("woff");
}
@font-face {
  font-family: "Cygre";
  font-style: italic;
  font-display: swap;
  font-weight: 500;
  src: local("Cygre"), url("../fonts/Cygre-MediumIt.woff2") format("woff2"), url("../fonts/Cygre-MediumIt.woff") format("woff");
}
@font-face {
  font-family: "Cygre";
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: local("Cygre"), url("../fonts/Cygre-SemiBold.woff2") format("woff2"), url("../fonts/Cygre-SemiBold.woff") format("woff");
}
/* Общие glob */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

body {
  margin: 0;
  overflow-y: auto;
  padding: 0;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: var(--shrift-tekst-na-fone);
  background: var(--fon);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  width: 100%;
}

.section-offset {
  margin-bottom: 150px;
}

.section__top {
  border-bottom: 1px solid var(--seryy);
  margin-bottom: 60px;
}
.section__top_title {
  margin-bottom: 52px;
}
.section__top_title span {
  font-style: italic;
}

/*  buttons */
.primary-btn, .secondary-btn, .tertiary-btn, .form-btn-green, .form-btn-white {
  display: block;
  border-radius: 5px;
  padding: 16px 30px 20px 30px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  transition: all 0.2s ease-in;
}

@media (hover: hover) {
  .primary-btn:hover::after, .secondary-btn:hover::after, .tertiary-btn:hover::after {
    transform: skewX(-45deg) scale(1, 1);
    transition: all 0.3s ease-out;
  }
}
.primary-btn {
  padding: 29px 30px 30px;
  background: var(--akcentnyy-zelenyy);
  color: var(--shrift-svetlyy);
  width: 100%;
}
.primary-btn:active {
  background: #83d2cc;
}

@media (hover: hover) {
  .primary-btn:hover {
    background: #8fe1da;
  }
}
.secondary-btn {
  border: 1px solid var(--akcentnyy-zelenyy);
  padding: 24px;
  color: var(--akcentnyy-zelenyy);
  text-align: center;
  width: 100%;
}
.secondary-btn:active {
  background: rgba(114, 206, 198, 0.8);
  color: var(--shrift-svetlyy);
}

@media (hover: hover) {
  .secondary-btn:hover {
    background: var(--akcentnyy-zelenyy);
    color: var(--shrift-svetlyy);
  }
}
.tertiary-btn {
  border: 1px solid var(--shrift-svetlyy);
  padding: 24px;
  border-radius: 5px;
  color: var(--shrift-svetlyy);
  width: 100%;
}
.tertiary-btn:active {
  background: rgba(251, 251, 254, 0.8);
  color: var(--osnovnoy-tusklyy);
}

@media (hover: hover) {
  .tertiary-btn:hover {
    background: var(--shrift-svetlyy);
    color: var(--osnovnoy-tusklyy);
  }
}
.form-btn-green {
  border: 1px solid #72cfc6;
  color: var(--akcentnyy-zelenyy);
}
.form-btn-green:active {
  background: rgba(114, 206, 198, 0.8);
  color: var(--shrift-svetlyy);
}

@media (hover: hover) {
  .form-btn-green:hover {
    background: var(--akcentnyy-zelenyy);
    color: var(--shrift-svetlyy);
  }
}
.form-btn-white {
  border: 1px solid var(--fon);
  background: var(--fon);
  color: var(--shrift-temnyy);
}
.form-btn-white:active {
  background-color: transparent;
  color: var(--shrift-svetlyy);
  opacity: 0.8;
}

@media (hover: hover) {
  .form-btn-white:hover {
    background-color: transparent;
    color: var(--shrift-svetlyy);
  }
}
.btn-more {
  display: block;
  font-weight: 400;
  line-height: 120%;
  letter-spacing: -0.02em;
  color: var(--shrift-svetlyy);
  position: relative;
  z-index: 2;
  transition: all 0.3s ease;
}
.btn-more::after {
  content: "";
  display: inline-block;
  width: 11px;
  height: 10px;
  background: url("./../img/icons/arrow-bottom-white.svg") no-repeat center/contain;
  margin-left: 8px;
  transition: all 0.3s ease;
}
.btn-more.show::after {
  transform: rotateX(180deg);
}
.btn-more.show::before {
  display: none;
}

@media (hover: hover) {
  .btn-more:hover {
    opacity: 0.5;
  }
  .btn-more:hover::after {
    transform: translate(0, 4px);
  }
  .btn-more.show:hover::after {
    transform: translate(0, -4px) rotate(180deg);
  }
}
.content-more {
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}
.content-more::after {
  content: "";
  display: block;
  width: 100%;
  height: 131px;
  background: linear-gradient(360deg, #acaddf 36.5%, rgba(172, 173, 223, 0) 100%);
  position: absolute;
  bottom: 0;
  left: 0;
}
.content-more.show::after {
  display: none;
}

/*  titles  */
.title, .title-litle, .title-ellipse {
  font-family: var(--second-family);
  font-weight: 500;
  line-height: 105%;
  letter-spacing: -0.02em;
}

.title {
  font-size: 64px;
  color: var(--shrift-temnyy);
}
.title span {
  color: var(--osnovnoy-tusklyy);
}

.title-litle {
  font-size: 20px;
  color: var(--shrift-svetlyy);
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 52px;
}
.title-litle::before {
  content: "";
  display: block;
  min-width: 10px;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: var(--shrift-svetlyy);
}

.title-ellipse {
  font-size: 42px;
  color: var(--shrift-temnyy);
  position: relative;
  min-height: 160px;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 40px;
}
.title-ellipse span {
  color: var(--akcentnyy-zelenyy);
}
.title-ellipse p {
  text-align: center;
}
.title-ellipse p::after {
  content: "?";
  color: var(--akcentnyy-zelenyy);
  transform: rotate(15deg);
  margin-left: 8px;
}
.title-ellipse.red span {
  color: var(--akcentnyy-krasnyy);
}
.title-ellipse.red p::after {
  content: none;
}
.title-ellipse.no-mark p::after {
  content: none;
}
.title-ellipse svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: calc(100% + 200px);
  min-height: 100%;
}
.title-ellipse__svg-mob {
  display: none;
}

.picture {
  display: block;
  width: 100%;
  min-height: 100%;
  border-radius: 5px;
  overflow: hidden;
}
.picture img {
  display: block;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*  form  */
.input, .textarea, .select {
  width: 100%;
  border: 1px solid rgba(251, 251, 254, 0.4);
  border-radius: 5px;
  padding: 24px;
  font-weight: 400;
  font-size: 18px;
  line-height: 105%;
  letter-spacing: -0.02em;
  color: var(--shrift-svetlyy);
  transition: all 0.3s ease;
}

.input::placeholder, .textarea::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 105%;
  letter-spacing: -0.02em;
  color: #c9f0ec;
}

.input:hover, .textarea:hover, .select:hover {
  border: 1px solid rgba(251, 251, 254, 0.8);
}

.input:focus, .input:active, .textarea:focus, .textarea:active, .select:focus, .select:active {
  border: 1px solid var(--fon);
}

.input-wrap {
  position: relative;
}
.input-wrap .label {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 105%;
  letter-spacing: -0.02em;
  color: var(--shrift-svetlyy);
  margin-bottom: 14px;
}

.input-error {
  display: none;
  position: absolute;
  bottom: -3px;
  left: 15px;
  font-size: 11px;
  color: #ff4a37;
}

.input-clear {
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translate(0, -50%);
  z-index: 5;
  cursor: pointer;
}

.textarea {
  outline: none;
  min-height: 142px;
  resize: none;
}

.select {
  cursor: pointer;
  outline: none;
  padding-right: 40px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: url(./../img/icons/arrow-bottom-white.svg) no-repeat right;
  background-position-x: calc(100% - 30px);
}
.select option {
  color: var(--shrift-temnyy);
}

.politic {
  font-size: 14px;
  line-height: 140%;
  color: var(--tekst-svetlyy);
}
.politic a {
  color: inherit;
  font-weight: 500;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  transition: all 0.3s ease;
}
.politic a:hover {
  opacity: 0.5;
}

/*  search  */
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.form-search {
  position: relative;
  width: 100%;
}
.form-search input {
  background: var(--fon-plashek);
  opacity: 0.7;
  width: 100%;
}
.form-search input:focus {
  opacity: 1;
}
.form-search img {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
}

/* accordion */
.accordion {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  cursor: pointer;
  width: 100%;
  position: relative;
}
.accordion--active .accordion__btn-more {
  transform: rotateX(180deg);
}
.accordion__content {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.accordion__btn-more {
  min-width: 10px;
}

/* tabs */
.tab-btn {
  transition: all 0.3s ease;
}

.tab-content {
  display: none;
}
.tab-content.active {
  display: block;
  width: 100%;
  min-height: 100%;
}

/* rating */
.full-stars {
  text-align: center;
}

.full-stars .rating-group {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.full-stars input {
  position: absolute;
  left: -9999px;
}

.full-stars label {
  margin: 0;
  cursor: pointer;
}

.full-stars label svg {
  margin: 0 2px;
  height: 18px;
  width: 20px;
  fill: var(--shrift-svetlyy);
  opacity: 1;
  -webkit-transition: fill 0.3s;
  -o-transition: fill 0.3s;
  transition: fill 0.3s;
}

.full-stars input:checked ~ label svg {
  opacity: 0.2;
}

.full-stars .rating-group:hover label svg {
  fill: var(--shrift-svetlyy);
  opacity: 1;
}

.full-stars .rating-group input:hover ~ label svg {
  opacity: 0.2;
}

/* breadcrumbs */
.breadcrumbs {
  margin-top: 20px;
  margin-bottom: 100px;
}

.breadcrumbs, .breadcrumbs li {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 8px;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.breadcrumbs li, .breadcrumbs li a {
  font-size: 16px;
  color: var(--shrift-temnyy);
}

.breadcrumbs li a {
  color: var(--shrift-tekst-na-fone);
  transition: all 0.2s ease-in;
}

.breadcrumbs li a:hover {
  color: var(--shrift-temnyy);
}

.breadcrumbs li:after {
  content: "—";
  display: block;
  font-size: 18px;
  font-weight: 700;
  color: var(--osnovnoy-tusklyy);
}

.breadcrumbs li:last-child::after {
  content: none;
}

/*  media global  */
@media (max-width: 800px) {
  .section-offset {
    margin-bottom: 80px;
  }
}
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
  .section__top {
    margin-bottom: 35px;
  }
  .section__top_title {
    margin-bottom: 28px;
  }
  .title {
    font-size: 32px;
  }
  .title-litle {
    font-size: 16px;
    margin-bottom: 30px;
  }
  .title-ellipse {
    font-size: 20px;
    min-height: 80px;
    margin-bottom: 15px;
  }
  .title-ellipse.long span::before {
    content: "\a";
    white-space: pre;
  }
  .title-ellipse svg {
    min-width: calc(100% + 100px);
    min-height: 100%;
  }
  .title-ellipse__svg {
    display: none;
  }
  .title-ellipse__svg-mob {
    display: block;
  }
  .primary-btn, .secondary-btn, .tertiary-btn, .form-btn-green, .form-btn-white {
    padding: 18px 25px 22px;
    font-size: 16px;
  }
  .btn-more::after {
    width: 8px;
    height: 6px;
  }
  .input, .textarea, .select {
    padding: 16px;
  }
  .select {
    padding-right: 26px;
    background-position-x: calc(100% - 16px);
  }
  .sort__text {
    margin-bottom: 14px;
  }
  .sort__item {
    margin-bottom: 14px;
  }
  .sort__item input[type=checkbox] {
    width: 22px;
    height: 22px;
  }
  .sort__item input[type=checkbox]::before {
    top: 3px;
    left: 7px;
    width: 6px;
    height: 10px;
    border-width: 0 2px 2px 0;
  }
  .sort__item label {
    font-size: 16px;
  }
  .breadcrumbs {
    margin-top: 12px;
    margin-bottom: 18px;
  }
  .breadcrumbs, .breadcrumbs li {
    gap: 4px;
  }
  .breadcrumbs li, .breadcrumbs li a {
    font-size: 14px;
  }
}
/* swiper CUSTOM */
.swiper {
  width: 100%;
  height: 100%;
}

/* swiper-buttons */
.swiper-btns {
  display: flex;
  align-items: center;
  gap: 6px;
}

.swiper-button-next, .swiper-button-prev,
.swiper-button-next--white, .swiper-button-prev--white {
  position: static;
  margin: 0;
  min-width: 40px;
  width: 40px;
  height: 40px;
  border-radius: 25px;
  background: rgba(61, 124, 133, 0.25);
  transition: all 0.3s ease;
}

.swiper-button-next:after, .swiper-button-prev:after {
  color: var(--tekst-svetlyy);
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
}

.swiper-button-next:hover, .swiper-button-prev:hover {
  background: var(--color-zelenyy);
}

.swiper-button-next:hover:after, .swiper-button-prev:hover:after {
  color: var(--belyy);
}

/* swiper-buttons--white */
.swiper-button-next--white, .swiper-button-prev--white {
  background: var(--belyy);
}

.swiper-button-next--white:after, .swiper-button-prev--white:after {
  color: var(--color-temno-zelenyy);
}

.swiper-button-next--white:hover, .swiper-button-prev--white:hover {
  background: rgba(61, 124, 133, 0.25);
}

.swiper-button-next--white:hover:after, .swiper-button-prev--white:hover:after {
  color: var(--color-temno-zelenyy);
}

/* swiper-pagination  */
.swiper-pagination {
  position: static;
  width: auto !important;
}
.swiper-pagination-bullet {
  position: static;
  border-radius: 10px;
  width: 25px;
  height: 3px;
  background: var(--color-zelenyy);
}

.swiper-pagination-fraction {
