}
.swiper-pagination-fraction .swiper-pagination-current {
  font-weight: 700;
  font-size: 26px;
  color: var(--belyy);
}

.swiper-scrollbar {
  position: static !important;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}
.swiper-scrollbar-drag {
  background: var(--belyy);
  border-radius: 10px;
}

.swiper-scrollbar-temnyy {
