.principles {
  padding: 100px 0;
  min-height: 100vh;
  background: var(--osnovnoy-tusklyy);
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
.principles__bg {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
}
.principles__bg img {
  width: 100%;
  height: 61px;
  object-fit: cover;
  object-position: center;
}
.principles__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
  grid-auto-flow: column;
  list-style-type: none;
  counter-reset: item;
}
.principles__item {
  border-radius: 5px;
  padding: 28px 44px 32px 32px;
  background: rgba(251, 251, 254, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  min-height: 303px;
  position: relative;
  border: 1.5px solid transparent;
  transition: all 0.5s ease-in-out;
}
.principles__item p:first-child {
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  color: var(--shrift-svetlyy);
}
.principles__item p:last-child {
  font-weight: 400;
  font-size: 18px;
  font-style: italic;
  line-height: 120%;
  color: #dfdff2;
}
.principles__item::before {
  content: counter(item, decimal-leading-zero);
  counter-increment: item;
  font-weight: 600;
  font-size: 28px;
  line-height: 120%;
  color: var(--osnovnoy-tusklyy);
  opacity: 0.6;
  position: absolute;
  transition: opacity 0.5s ease-in-out;
}
.principles__item:nth-child(1)::before {
  top: 20px;
  right: -78px;
}
.principles__item:nth-child(2)::before {
  bottom: 20px;
  right: -78px;
}
.principles__item:nth-child(4)::before {
  top: 20px;
  left: -78px;
}
.principles__item:nth-child(5)::before {
  bottom: 20px;
  left: -78px;
}
.principles__image {
  grid-row: span 2;
  border-radius: 5px;
  background: var(--fon);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

