    width: auto;
    height: 75px;
  }
  .principles__grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: auto;
    grid-auto-flow: row;
    gap: 24px;
    position: relative;
  }
  .principles__item {
    order: 1;
    padding: 0;
    background: transparent;
    gap: 8px;
    min-height: auto;
    position: static;
    width: 85%;
    margin-left: auto;
    border: none;
    opacity: 1;
    transform: 1;
  }
  .principles__item:nth-child(1)::before {
    top: 20px;
    left: 76%;
    bottom: auto;
    right: auto;
  }
  .principles__item:nth-child(2)::before {
    top: 60px;
    left: 76%;
    bottom: auto;
    right: auto;
  }
  .principles__item:nth-child(4)::before {
    top: 100px;
    left: 76%;
    bottom: auto;
    right: auto;
