
// methods
.methods {

    &__wrap {
        padding: 150px 0;
        min-height: 100vh;
        position: sticky;
        top: 0;
        &.purple {
            background: var(--osnovnoy-tusklyy);
        }
        &.green {
            background: var(--akcentnyy-zelenyy);
            & .methods__list span {
                color: #c9f0ec;
            }
        }
        &.red {
            background: var(--akcentnyy-krasnyy);
            & .methods__list span {
                color: #f2c2c5;
            }
        }
    }

    &__top {
        margin-bottom: 100px;
    }

    &__paginations {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 10px;
    }

    &__stars {
        display: flex;
        gap: 8px;
    }

    &__pagination {
        border: 1px solid var(--shrift-svetlyy);
        border-radius: 30px;
        padding: 10px 15px 8px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-svetlyy);
        width: fit-content;
    }

    &__name {
        font-weight: 500;
        font-size: 54px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-svetlyy);
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__text {
        flex-basis: 573px;
    }

    &__picture {
        width: fit-content;
        & img {
            max-width: 880px;
        }
    }

    &__list {
        & ul {
            padding-left: 50px;
            margin-bottom: 32px;
            & li {
                &:not(:last-child) {
                    padding-bottom: 20px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    margin-bottom: 16px;
                }
                
                & p {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 120%;
                    color: var(--shrift-svetlyy);
                    display: flex;
                    gap: 8px;
                    margin-bottom: 6px;
                    &::before{
                        content: "";
                        display: block;
                        min-width: 10px;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                        margin-top: 5px;
                    }
                }

                & span {
                    display: block;
                    font-style: italic;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 120%;
                    color: #dfdff2;
                }
            }
        }
    }
}

// media methods
@media (max-width: 960px) {
    .methods {
        &__row {
            display: block;
        }
        &__text {
            flex-basis: auto;
        }
        &__picture {
            display: none;
        }
    }
}

@media (max-width: 768px) {
    .methods {
        padding: 50px 0;
    
        &__top {
            margin-bottom: 33px;
        }

        &__stars img {
            width: 12px;
            height: 12px;
        }
    
        &__name {
            font-size: 28px;
        }

        &__row {
            display: block;
        }
    
        &__list {
            & ul {
                padding-left: 44px;
                margin-bottom: 28px;
                & li {
                    &:not(:last-child) {
                        padding-bottom: 14px;
                        margin-bottom: 10px;
                    }
                    
                    & p {
                        font-size: 16px;
                        margin-bottom: 6px;
                        &::before{
                            min-width: 8px;
                            width: 8px;
                            height: 8px;
                            margin-top: 5px;
                        }
                    }
    
                    & span {
                        font-size: 16px;
                    }
                }
            }
        }
    }
    
}