
// rehab
.rehab {
    overflow: hidden;

    &__top {
        display: flex;
        gap: 40px;
        margin-bottom: 80px;
    }
    &__title {
        flex-basis: 50%;
    }
    &__subtitle {
        flex-basis: 50%;
        max-width: 650px;
        font-style: italic;
    }

    &__swiper {
        overflow: visible;
        margin-bottom: 44px;
    }

    &__image {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 16px;

        &_text {
            color: var(--shrift-tekst-na-fone);
            & p {
                font-weight: 500;
                font-size: 20px;
                line-height: 105%;
                letter-spacing: -0.02em;
                color: var(--shrift-temnyy);
                margin-bottom: 8px;
            }
            & span {
                font-style: italic;
            }

            &.bottom {
                padding-top: 12px;
                border-top: 1px solid var(--seryy);
            }
            &.top {
                padding-bottom: 12px;
                border-bottom: 1px solid var(--seryy);
            }
        }
    }

    &__picture {
        & img {
            max-height: 260px;
            min-height: 260px;
        }
    }

    &__excursion {
        border-radius: 5px;
        padding: 24px 32px;
        background: var(--osnovnoy-tusklyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        position: relative;
        overflow: hidden;
        margin-top: 24px;

        &_text {
            flex: 1 0 45%;
            font-weight: 500;
            font-size: 36px;
            line-height: 105%;
            color: var(--shrift-svetlyy);
        }
        &_btn {
            flex: 1 0 45%;
        }
        &_img {
            display: block;
            width: 77px;
            height: 95px;
            object-fit: cover;
            position: absolute;
            top: 10px;
            left: -20px;
        }
    }
}

// media rehab
@media (max-width: 1024px) {
    .rehab {
        &__top {
            flex-direction: column;
            gap: 20px;
        }
        &__title {
            flex-basis: 100%;
        }
        &__subtitle {
            flex-basis: 100%;
            max-width: none;
        }
        &__excursion {
            flex-direction: column;
            align-items: stretch;
            &_img {
                top: 10px;
                left: auto;
                right: -20px;
            }
        }
    }
}
@media (max-width: 768px) {
    .rehab {
        padding: 70px 0;
        &__top {
            gap: 12px;
            margin-bottom: 30px;
        }
        &__swiper {
            margin-bottom: 28px;
        }
        &__image {
            &_text {
                & p {
                    font-size: 16px;
                }
            }
        }
        &__picture {
            & img {
                max-height: 200px;
                min-height: 180px;
            }
        }
        &__excursion {
            padding: 16px;
            gap: 24px;
            margin-top: 15px; 
            &_text {
                font-size: 24px;
            }
        }
    }
}