
// contacts
.contacts {
    background: #a3a5d9;
    position: relative;
    display: flex;
    overflow: hidden;
    
    &__content {
        flex: 0 0 100%;
    }

    &__body {
        width: 50%;
        padding: 150px 0;
        padding-right: 80px;
    }

    &__maps {
        position: relative;
        flex: 0 0 50%;
        transform: translate(-100%, 0);
        z-index: 2;
        min-height: 100px;
        background: #565778;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-left: 50px;
        margin-bottom: 24px;
    }

    &__item {
        padding-bottom: 29px;
        border-bottom: 1px solid rgba(251,251,254,0.15);
        
        & span {
            display: block;
            font-style: italic;
            color: #dfdff2;
            padding-left: 18px;
            position: relative;
            margin-bottom: 8px;

            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                position: absolute;
                top: 4px;
                left: 0;
            }
        }

        & a, & p {
            font-weight: 500;
            font-size: 28px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            transition: all 0.2s ease-in;
        }

        & a:hover {
            opacity: 0.8;
        }
    }

    &__network {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80px;
        height: 80px;
        border-radius: 5px;
        border: 1px solid rgba(251, 251, 254, 0.2);
        margin-left: 50px;
        transition: all 0.2s ease-in;
        
        &:hover {
            border: 1px solid var(--fon);
        }
    }
}

.contacts-page {
    background: transparent;
    .contacts {
        &__item {
            border-bottom: 1px solid var(--seryy);
            & span {
                color: var(--shrift-tekst-na-fone);
                &::before {
                    background: url("./../img/icons/star.svg") no-repeat center / contain;
                }
            }
            & a, & p {
                color: var(--shrift-temnyy);
            }
        }
    
        &__network {
            border: 1px solid rgba(172, 173, 223, 0.2);
            &:hover {
                border: 1px solid rgba(172, 173, 223, 0.8);
            }
        }
    }
}

// media contacts
@media (max-width: 1024px) {
    .contacts {
        flex-direction: column;
        &__body {
            width: 100%;
            padding-right: 0;
        }
        &__maps {
            flex: 0 0 100%;
            transform: translate(0, 0);
            min-height: 600px;
        }
    }
}
@media (max-width: 768px) {
    .contacts {
        &__body {
            padding: 70px 0;
        }
        &__maps {
            min-height: 350px;
        }
        &__list {
            gap: 12px;
            margin-left: 44px;
        }
    
        &__item {
            padding-bottom: 16px;
            
            & span {
                padding-left: 14px;
                &::before {
                    width: 8px;
                    height: 8px;
                }
            }
    
            & a, & p {
                font-size: 22px;
            }
        }
    
        &__network {
            width: 60px;
            height: 60px;
        }
    }

    .contacts-page {
        .contacts {
            &__body {
                padding:  0 0 70px 0;
            }
        }
    }
}