
// stocks
.stocks {
    padding: 150px 0;
    background: var(--fon-blokov);
    overflow: hidden;

    &__title {
        margin-bottom: 80px;
    }

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        margin-bottom: 30px;
    }

    &__card {
        border-radius: 5px;
        padding: 20px;
        box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
        background: var(--fon);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 80px;
        height: 100%;
        min-height: 380px;
        position: relative;
        overflow: hidden;
        color: var(--shrift-tekst-na-fone);
        text-align: left;

        &:hover {
            & .stocks__card_percent-img {
                animation: rotateStar 9s linear infinite;
            }
        }

        &_date {
            font-size: 16px;
            color: var(--osnovnoy-tusklyy);
        }

        &_title {
            font-weight: 500;
            font-size: 22px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            margin-bottom: 12px;
        }

        &_subtitle {
            font-style: italic;
            line-height: 120%;
        }

        &_percent {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 153px;
            height: 153px;
            position: absolute;
            top: -36px;
            right: -32px;

            &-img {
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            & p {
                font-weight: 500;
                font-size: 36px;
                line-height: 105%;
                letter-spacing: -0.02em;
                color: var(--shrift-svetlyy);
                position: relative;
                z-index: 2;
            }
        }
    }
}

// media stocks
@media (max-width: 768px) {
    .stocks {
        padding: 70px 0;
        &__title {
            margin-bottom: 30px;
        }
        &__card {
            padding: 16px;
            min-height: auto;
            &_title {
                font-size: 18px;
            }
            &_percent {
                width: 90px;
                height: 90px;
                top: -15px;
                right: -15px;
                & p {
                    font-size: 26px;
                }
            }
        }
    }
}