// popup cokie
.popup-widget {
    position: fixed;
    left: 20px;
    bottom: 40px;
    z-index: 200;
    transition: all 0.3s ease;
    
    &__wrap {
        border-radius: 45px;
        padding: 10px;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
        background: #fff;
        width: fit-content;
        height: fit-content;
        transition: all 0.3s ease;
    }
    
    &__plus {
        border-radius: 40px;
        background: var(--osnovnoy-tusklyy);
        border: 1px solid var(--osnovnoy-tusklyy);
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        
        & svg path {
            transition: all 0.3s ease;
        }
        
        &:hover {
            background: #9293d8;
        }
    }

    &__content {
        max-width: 0;
        opacity: 0;
        transition: all 0.4s ease;
        &.accordion__content--active {
            max-width: 320px;
            opacity: 1;
        }
    }

    &__btn {
        padding: 17px 20px;
        border-radius: 5px;
        font-style: italic;
        font-weight: 500;
        font-size: 16px;
        color: var(--shrift-svetlyy);
        display: flex;
        align-items: center;
        gap: 10px;
        width: 100%;
        transition: all 0.3s ease;
        &.purple {
            background: var(--osnovnoy-tusklyy);
            margin-bottom: 6px;
        }
        &.green {
            background: var(--akcentnyy-zelenyy);
            margin-bottom: 10px;
        }
        &:hover {
            &.purple {
                background: #9293d8;
            }
            &.green {
                background: #8fe1da;
            }
        }
    }
    
    &.open {
        .popup-widget__wrap {
            border-radius: 5px;
        }
        .popup-widget__plus {
            background: #fff;
            transform: rotate(45deg);
            & svg path {
                fill: var(--osnovnoy-tusklyy);
            }
        }
    }
}

// media popup cokie
@media (max-width: 970px) {
    .popup-widget {
        left: 10px;
        bottom: 30px;
    }
}
@media (max-width: 600px) {
    .popup-widget {
        &__wrap {
            padding: 8px;
        }
        &__plus {
            width: 36px;
            height: 36px;
        }
        &__btn {
            padding: 10px 14px;
            font-size: 14px;
            gap: 8px;
            & img {
                width: 20px;
                height: 20px;
                object-fit: cover;
            }
        }
    }
}
@media (max-width: 400px) {
    .popup-widget {
        &.open {
            max-width: calc(100% - 10px);
            width: 100%;
            .popup-widget__wrap {
                max-width: calc(100% - 10px);
                width: 100%;
            }
        }
    
        &__content {
            max-width: 0;
            &.accordion__content--active {
                max-width: 380px;
            }
        }
    }
}