
// principles
.principles {
    padding: 100px 0;
    min-height: 100vh;
    background: var(--osnovnoy-tusklyy);
    position: relative;
    overflow: hidden;
    transition: all 0.5s ease-in-out;

    &__bg {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        right: 0;
        & img {
            width: 100%;
            height: 61px;
            object-fit: cover;
            object-position: center;
        }
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 20px;
        grid-auto-flow: column; 
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        border-radius: 5px;
        padding: 28px 44px 32px 32px;
        background: rgba(251, 251, 254, 0.1);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        min-height: 303px;
        position: relative;
        border: 1.50px solid transparent;
        transition: all 0.5s ease-in-out;

        & p:first-child {
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            color: var(--shrift-svetlyy);
        }

        & p:last-child {
            font-weight: 400;
            font-size: 18px;
            font-style: italic;
            line-height: 120%;
            color: #dfdff2;
        }

        &::before {
            content: counter(item, decimal-leading-zero); 
            counter-increment: item;
            font-weight: 600;
            font-size: 28px;
            line-height: 120%;
            color: var(--osnovnoy-tusklyy);
            opacity: 0.6;
            position: absolute;
            transition: opacity 0.5s ease-in-out;
        }

        &:nth-child(1)::before {top: 20px; right: -78px;}
        &:nth-child(2)::before {bottom: 20px; right: -78px;}
        &:nth-child(4)::before {top: 20px; left: -78px;}
        &:nth-child(5)::before {bottom: 20px; left: -78px;}
    }

    &__image {
        grid-row: span 2;
        border-radius: 5px;
        background: var(--fon);
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;
    }
}


// media principles
@media (max-width: 1024px) {
    .principles {
        &__bg {
            & img {
                width: auto;
                height: 75px;
            }
        }
    
        &__grid {
            display: grid;
            grid-template-columns: repeat(1, 1fr);
            grid-template-rows: auto;
            grid-auto-flow: row; 
            gap: 24px;
            position: relative;
        }
    
        &__item {
            order: 1;
            //display: none;
            padding: 0;
            background: transparent;
            gap: 8px;
            min-height: auto;
            position: static;
            width: 85%;
            margin-left: auto;
    
            border: none;
            opacity: 1;
            transform: 1;
    
            &:nth-child(1)::before {top: 20px; left: 76%; bottom: auto; right: auto;}
            &:nth-child(2)::before {top: 60px; left: 76%; bottom: auto; right: auto;}
            &:nth-child(4)::before {top: 100px; left: 76%; bottom: auto; right: auto;}
            &:nth-child(5)::before {top: 140px; left: 76%; bottom: auto; right: auto;}

            .animation-visible {
                display: block;
            }
        }
    
        &__image {
            grid-row: span 1;
            order: 0;
            border-radius: 0 5px 5px 0;
            padding: 28px;
            margin-left: -10px;
            width: 85%;
        }
    }
}
@media (max-width: 768px) {
    .principles {
        padding: 70px 0 100px;
        &__item {
            & p:first-child {
                font-size: 20px;
            }
            & p:last-child {
                font-size: 16px;
            }
            &::before {
                font-size: 14px;
                opacity: 0.4;
            }
            &:nth-child(1)::before {top: 10px; left: 74%;}
            &:nth-child(2)::before {top: 39px; left: 74%;}
            &:nth-child(4)::before {top: 68px; left: 74%;}
            &:nth-child(5)::before {top: 97px; left: 74%;}
        }
        &__image {
            & img {
                max-width: 130px;
            }
        }
    }
}