
// steps
.steps {
    overflow: hidden;
    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        list-style-type: none;
        counter-reset: item;
    }

    &__card {
        border-radius: 5px;
        padding: 32px 32px 32px 50px;
        background: var(--fon-blokov) url("./../img/steps/steps-bg.svg") no-repeat;
        background-position: bottom;
        background-size: 100% 280px; 
        border: 1px solid transparent;
        min-height: 460px;
        position: relative;
        overflow: hidden;
        transition: all 0.5s ease;

        & .accordion, & .accordion__content {
            display: contents;
        }
        & .accordion__btn-more {
            display: none;
            min-width: 9px;
        }
        
        &:hover {
            background-position: center calc(220px + 100%); 
            border: 1px solid #d0d1ec;
            &::before {
                opacity: 0.5;
            }
            & .steps__card_list {
                transform: translate(0, 0);
            }
        }

        &::before {
            content: counter(item, decimal-leading-zero); 
            counter-increment: item;
            display: block;
            font-style: italic;
            font-weight: 500;
            font-size: 42px;
            line-height: 120%;
            color: var(--osnovnoy-tusklyy);
            opacity: 0.15;
            position: absolute;
            top: 24px;
            left: -18px;
            transition: all 0.5s ease;
        }

        &_top {
            display: flex;
            align-items: flex-start;
            gap: 16px;
            margin-bottom: 45px;
        }

        &_icon {
            display: block;
            min-width: 50px;
            width: 50px;
            height: 50px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &_name {
            margin-bottom: 16px;
            & p {
                font-weight: 500;
                font-size: 24px;
                line-height: 120%;
                color: var(--shrift-temnyy);
                margin-bottom: 8px;
            }

            & span {
                display: block;
                font-style: italic;
                font-size: 16px;
                line-height: 120%;
                color: var(--shrift-tekst-na-fone);
            }
        }

        &_description {
            font-style: italic;
            font-size: 16px;
            line-height: 120%;
            color: var(--osnovnoy-tusklyy);
            margin-bottom: 33px;
        }

        &_list {
            height: calc(100% - 80px);
            transform: translate(0, 100%);
            transition: all 0.5s ease;
            & p {
                font-style: italic;
                font-weight: 500;
                font-size: 16px;
                line-height: 120%;
                color: var(--osnovnoy-tusklyy);
                margin-top: 28px;
                margin-bottom: 16px;
            }
        }

        &_list ul li {
            color: var(--shrift-temnyy);
            position: relative;
            padding-left: 18px;
            &:not(:last-child) {
                padding-bottom: 16px;
                border-bottom: 1px solid #e6e6eb;
                margin-bottom: 12px;
            }
            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/star.svg") no-repeat center / contain ;
                position: absolute;
                top: 3px;
                left: 0;
            }
        }

        &_specialists ul {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 6px;
            & li:nth-child(n) {
                border-radius: 5px;
                padding: 7px 14px;
                background: var(--osnovnoy-tusklyy);
                color: var(--shrift-svetlyy);
                border: none;
                margin: 0;
                &::before {
                    content: none;
                }
            }
        }

        &.red {
            background: rgba(205, 127, 132, 0.08) url("./../img/steps/steps-bg--red.svg") no-repeat;
            background-position: bottom;
            background-size: 100% 280px; 
            &:hover {
                background-position: center calc(220px + 100%); 
                border: 1px solid #e2b8bd;
            }

            &::before {
                color: var(--akcentnyy-krasnyy);
            }

            & .steps__card_description {
                color: var(--akcentnyy-krasnyy);
            }

            & .steps__card_list ul li {
                &::before {
                    background: url("./../img/icons/star-red.svg") no-repeat center / contain ;
                }
            }
        }

        &.psycho {
            padding: 32px;
            &::before {
                content: none; 
            }
        }
    }


    &__blocks {
        display: flex;
        gap: 20px;
        padding-top: 30px;
        border-top: 1px solid var(--seryy);
        margin-top: 30px;
    }

    &__block {
        flex: 1 0 45%;
        padding: 24px;
        border-radius: 5px;
        color: var(--shrift-svetlyy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 60px;

        &--purple {background: var(--osnovnoy-tusklyy);}
        &--green {background:  var(--akcentnyy-zelenyy);}
        &--red {background: var(--osnovnoy-tusklyy);}
        &--yellow {
            background: var(--akcentnyy-zheltyy);
            color: var(--shrift-temnyy);
            & .steps__block_list ul  li::before {
                background: url("./../img/icons/star-black.svg") no-repeat center / contain ;
            }
        }

        &_title {
            font-weight: 500;
            font-size: 28px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
            &::after {
                content: "*";
                font-weight: 400;
                transform: rotate(15deg);
            }
        }

        &_subtitle {
            font-style: italic;
            color: #dfdff2;
            margin-top: 12px;
            max-width: 50%;
        }

        &_spase-between {
            display: flex;
            justify-content: space-between;
            gap: 14px;
        }

        &_list {
            & ul {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                gap: 12px;
    
                & li {
                    flex-basis: 46%;
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    position: relative;
                    padding-left: 18px;
                    &::before {
                        content: "";
                        display: block;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star-white.svg") no-repeat center / contain ;
                        position: absolute;
                        top: 3px;
                        left: 0;
                    }
    
                    & i {
                        font-size: 16px;
                        color: #dfdff2;
                    }
    
                    & span {
                        font-weight: 500;
                        font-size: 36px;
                    }
                }
            }
        }

        &_list.fill ul li {
            flex-basis: 100%;
        }
    }


    &__help {
        border-radius: 5px;
        padding: 24px;
        background: var(--akcentnyy-krasnyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        position: relative;
        overflow: hidden;
        margin-top: 24px;

        &_text {
            flex: 1 0 45%;
            font-weight: 500;
            font-size: 36px;
            line-height: 105%;
            color: var(--shrift-svetlyy);
        }
        &_btn {
            flex: 1 0 45%;
        }
        &_img {
            display: block;
            width: 85px;
            height: 95px;
            object-fit: cover;
            position: absolute;
            top: 20px;
            left: -35px;
        }
    }
}

// media steps
@media (max-width: 1200px) {
    .steps {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
        &__blocks {
            flex-direction: column;
        }
        &__help {
            flex-direction: column;
            align-items: stretch;
            &_img {
                top: 10px;
                left: auto;
                right: -20px;
            }
        }
    }
}
@media (max-width: 768px) {
    .steps {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 8px;
        }
    
        &__card {
            padding: 16px 16px 20px 44px;
            min-height: auto;
            background: var(--fon-blokov) url("./../img/steps/steps-bg-mobile.svg") no-repeat;
            background-size:  100% auto; 
            background-position: center calc(60px + 100%); 
            &:hover {
                background-position: center calc(15px + 100%); 
            }

            &.red {
                background: rgba(205, 127, 132, 0.08) url("./../img/steps/steps-bg-mobile--red.svg") no-repeat;
                background-size:  100% auto; 
                background-position: center calc(60px + 100%); 
                &:hover {
                    background-position: center calc(15px + 100%); 
                }
            }

            & .accordion {
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                gap: 20px;
            }
            & .accordion__content, & .accordion__btn-more {
                display: block;
            }
    
            &::before {
                font-size: 34px;
                top: 16px;
                left: -14px;
            }

            &_top {
                flex-direction: column;
                gap: 12px;
                margin-bottom: 0;
            }

            &_icon {
                min-width: 36px;
                width: 36px;
                height: 36px;
            }
    
            &_name {
                margin-bottom: 0;
                & p {
                    font-size: 18px;
                    margin-bottom: 6px;
                }
                & span {
                    font-size: 14px;
                }
            }
    
            &_description {
                font-size: 14px;
                margin-top: 16px;
                margin-bottom: 33px;
            }

            &_list {
                height: auto;
                transform: translate(0, 0);
                margin-bottom: 40px;
                & p {
                    margin-bottom: 12px;
                }
            }
    
            &_list ul li {
                padding-left: 14px;
                &:not(:last-child) {
                    padding-bottom: 14px;
                    margin-bottom: 10px;
                }
                &::before {
                    width: 8px;
                    height: 8px;
                }
            }

            
        &_specialists ul {
            & li:nth-child(n) {
                padding: 7px 14px;
                border: none;
                margin: 0;
            }
        }

            &.psycho {
                padding: 16px;
                & .accordion__content--active .steps__card_list {
                    margin-top: 28px;
                }
            }
        }


        &__blocks {
            gap: 8px;
            padding-top: 24px;
            margin-top: 24px;
        }
    
        &__block {
            padding: 24px 16px;
            gap: 28px;
            &_title {
                font-size: 22px;
            }
            &_subtitle {
                max-width: 100%;
            }

            &_list {
                & ul {
                    flex-direction: column;
                    gap: 10px;
                    & li {
                        flex-basis: 100%;
                        padding-bottom: 12px;
                        padding-left: 14px;
                        &:last-child {
                            padding-bottom: 0;
                            border-bottom: none;
                        }
                        &::before {
                            width: 8px;
                            height: 8px;
                        }
                        & i {
                            font-size: 14px;
                        }
                        & span {
                            font-size: 28px;
                        }
                    }
                }
            }
        }

        &__help {
            padding: 16px;
            gap: 24px;
            margin-top: 15px; 
            &_text {
                font-size: 24px;
            }
        }
    }
}