// popup-search-2

.popup-search {
    &__body {
        display: flex;
        justify-content: flex-end;
        align-items: stretch;
        padding: 0;
    }
    &__content {
        padding: 30px 0;
        background: var(--osnovnoy-tusklyy);
        max-width: 100%;
        height: max-content;
        max-height: 100vh;
    }

    &__top {
        display: flex;
        align-items: center;
        gap: 30px;
    }

    &__form {
        position: relative;
        flex-grow: 1;
        &_input {
            padding-left: 54px;
            &::placeholder {
                color: var(--shrift-svetlyy);
            }
        }
    
        &_img {
            position: absolute;
            top: 50%;
            left: 24px;
            transform: translate(0, -50%);
        }
    }


    &__search {
        margin-top: 16px;
        &-result {
            max-height: calc(100vh - 170px);
            margin-top: 40px;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        & li a {
            display: block;
            border-radius: 5px;
            padding: 10px;
            background: rgba(251, 251, 254, 0.1);
            border: 1px solid transparent;
            color: var(--shrift-svetlyy);
            transition: all 0.3s ease;
            &:hover {
                border: 1px solid var(--shrift-svetlyy);
            }
        }
    }

    &__list_result {
        & li a {
            display: block;
            padding: 16px 0 20px;
            border-bottom: 1px solid rgba(251, 251, 254, 0.15);
            margin-right: 8px;
            &:hover {
                & p, & span {
                    color: rgba(251, 251, 254, 0.8);
                }
            }
            & p {
                font-weight: 500;
                font-size: 22px;
                line-height: 105%;
                letter-spacing: -0.02em;
                color: var(--shrift-svetlyy);
                margin-bottom: 12px;
                transition: all 0.3s ease;
            }
            & span {
                font-style: italic;
                color: #dfdff2;
                position: relative;
                display: block;
                padding-left: 14px;
                transition: all 0.3s ease;
                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                    position: absolute;
                    top: 2px;
                    left: 0;
                }
            }
        }
    }

    &__close {
        width: 24px;
        height: 24px;
        position: static;
    }
}

// media popup-search-2
@media (max-width: 768px) {
    .popup-search {
        &__content {
            padding: 20px 0 24px;
        }
        &__top {
            gap: 12px;
        }
        &__form {
            position: relative;
            &_input {
                padding: 16px 16px 16px 44px;
            }
            &_img {
                left: 16px;
            }
        }
        &__search {
            margin-top: 16px;
            &-result {
                max-height: calc(100vh - 120px);
                margin-top: 20px;
            }
        }
        &__list {
            gap: 4px;
            & li a {
                font-size: 14px;
            }
        }
        &__list_result {
            & li a {
                padding: 12px 0 14px;
                &:hover {
                    & p, & span {
                        color: rgba(251, 251, 254, 0.8);
                    }
                }
                & p {
                    font-size: 18px;
                    margin-bottom: 8px;
                }
                & span {
                    &::before {
                        width: 8px;
                        height: 8px;
                    }
                }
            }
        }
        &__close {
            width: 14px;
            height: 14px;
        }
    }
}
