
// reviews
.reviews {
    overflow: hidden;

    &__title {
        max-width: 880px;
        margin-bottom: 80px;
    }

    &__swiper {
        overflow: visible;
    }

    &__card {

        &_inner {
            border-radius: 5px 5px 0 0;
            padding: 20px;
            background: var(--fon-blokov);
        }

        &_tag {
            font-weight: 500;
            font-size: 16px;
            line-height: 120%;
            color: var(--osnovnoy-tusklyy);
            position: relative;
            padding-left: 18px;
            margin-bottom: 40px;

            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/star.svg") no-repeat center / contain;
                position: absolute;
                top: 3px;
                left: 0;
            }
        }

        &_content {
            margin-bottom: 40px;
        }

        &_quote {
            font-weight: 500;
            font-size: 28px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            display: flex;
            gap: 16px;
            margin-bottom: 24px;

            &::before {
                content: "";
                display: block;
                min-width: 36px;
                width: 36px;
                height: 31px;
                background: url("./../img/icons/quote.svg") no-repeat center / contain;;
            }
        }

        &_text {
            font-style: italic;
            margin-bottom: 12px;
            max-height: 88px;
            overflow: hidden;
            transition: all 0.3s ease;
        }

        &_btn-more {
            color: var(--akcentnyy-zelenyy);
            &::after {
                background: url("./../img/icons/arrow-bottom-green.svg") no-repeat center / contain;
            }
        }

        &_author {
            padding-top: 16px;
            border-top: 1px solid #e6e6eb;

            & p {
                font-weight: 500;
                line-height: 120%;
                color: var(--shrift-temnyy);
                margin-bottom: 4px;
            }

            & span {
                font-size: 16px;
                font-style: italic;
            }
        }
    }

    &__answer {
        border-radius: 0 0 5px 5px;
        padding: 16px;
        background: #78cfc7;
        color: var(--shrift-svetlyy);

        &_btn {
            font-weight: 500;
            font-size: 20px;
            display: flex;
            justify-content: space-between;
            gap: 20px;

            &.accordion--active {
                margin-bottom: 24px;
            }

            & span {
                position: relative;
                padding-left: 18px;
    
                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star-white.svg") no-repeat center / contain;;
                    position: absolute;
                    top: 3px;
                    left: 0;
                }
            }
        }

        &_doctor {
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 500;
            line-height: 120%;
            padding-bottom: 16px;
            border-bottom: 1px solid var(--fon);
            margin-bottom: 12px;
        }

        &_photo {
            display: block;
            border-radius: 40px;
            min-width: 60px;
            width: 60px;
            height: 60px;
            background: #fff;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }

        &_text {
            font-style: italic;
            line-height: 120%;
            color: #c9f0ec;
        }
    }
}

.reviews-page {
    &__sort {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 80px;
        &_wrap {
            max-width: 50%;
            width: 100%;
            & .label {
                color: var(--shrift-tekst-na-fone);
            }
        }
    }

    &__select {
        border: 1px solid rgba(46, 55, 73, 0.3);
        color: var(--shrift-temnyy);
        background: url(./../img/icons/arrow-bottom-black.svg) no-repeat right;
        background-position-x: calc(100% - 30px);
        &:hover {
            border: 1px solid rgba(46, 55, 73, 0.5);
        }
        &:focus {
            border: 1px solid var(--shrift-temnyy);
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }
}

// media reviews
@media (max-width: 1200px) {
    .reviews-page {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}

@media (max-width: 768px) {
    .reviews {
        &__title {
            margin-bottom: 30px;
        }

        &__card {
            &_inner {
                padding: 16px;
            }
            &_tag {
                font-size: 14px;
                padding-left: 14px;
                margin-bottom: 30px;
                &::before {
                    width: 8px;
                    height: 8px;
                    top: 3px;
                    left: 0;
                }
            }
            &_content {
                margin-bottom: 30px;
            }
            &_quote {
                font-size: 20px;
                gap: 7px;
                margin-bottom: 16px;
                &::before {
                    min-width: 21px;
                    width: 21px;
                    height: 18px;
                }
            }
            &_text {
                margin-bottom: 8px;
                max-height: 133px;
            }
            &_author {
                padding-top: 10px;
                border-top: 1px solid #e6e6eb;
                & span {
                    font-size: 14px;
                }
            }
        }
    
        &__answer {
            &_btn {
                font-size: 18px;
                &.accordion--active {
                    margin-bottom: 20px;
                }
                & span {
                    padding-left: 14px;
                    &::before {
                        width: 8px;
                        height: 8px;
                    }
                }
            }
            &_photo {
                min-width: 40px;
                width: 40px;
                height: 40px;
            }
        }
    }

    .reviews-page {
        &__sort {
            margin-bottom: 30px;
            &_wrap {
                max-width: 100%;
            }
        }
        &__select {
            background-position-x: calc(100% - 16px);
        }
        &__quantity {
            display: none;
        }
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 12px;
        }
    }
}