
// stories
.stories {
    overflow: hidden;

    &__top {
        display: flex;
        gap: 40px;
        margin-bottom: 80px;
    }
    &__title {
        flex-basis: 50%;
    }
    &__subtitle {
        flex-basis: 50%;
        max-width: 650px;
        font-style: italic;
    }

    &__swiper {
        overflow: visible;
    }

    &__card {
        border-radius: 5px;
        padding-top: 24px;
        background: var(--osnovnoy-tusklyy);
        display: flex;
        flex-direction: column;
        overflow: hidden;


        &_wrap {
            padding-right: 24px;
            padding-left: 24px;
        }

        &_text {
            font-style: italic;
            font-size: 16px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: #dfdff2;
        }

        &_name {
            padding-left: 12px;
            border-left: 3px solid var(--shrift-svetlyy);
            font-style: italic;
            line-height: 120%;
            letter-spacing: -0.02em;
            margin-bottom: 24px;

            & p {
                font-weight: 500;
                font-size: 24px;
                color: var(--shrift-svetlyy);
            }

        }

        &_blocks {
            display: flex;
            flex-direction: column;
            gap: 8px;
            margin-bottom: 20px;
        }

        &_block {
            padding: 12px 24px;
            margin: 0 -24px;
            background: rgba(251, 251, 254, 0.15);
            & p {
                color: var(--shrift-svetlyy);
            }
        }

        &_list {
            margin-bottom: 24px;
            & p {
                margin-bottom: 12px;
            }
            & ul li {
                color: var(--shrift-svetlyy);
                padding-left: 18px;
                position: relative;

                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(251,251,254,0.15);
                    margin-bottom: 12px;
                }

                &::before {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: url("./../img/icons/star-white.svg") no-repeat center / contain;
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }
    }

    &__quote {
        border: 2px solid var(--osnovnoy-tusklyy);
        border-radius: 5px;
        padding: 24px;
        background: var(--fon);

        &_img-marks {
            display: block;
            width: 25px;
            height: 21px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 12px;
        }

        &_text {
            font-style: italic;
            color: var(--shrift-temnyy);
            max-height: 46px;
            overflow: hidden;
            margin-bottom: 8px;
        }
        
        &_btn {
            color: var(--osnovnoy-tusklyy);
            position: relative;
            padding-right: 18px;
            &::after {
                content: "";
                display: block;
                width: 10px;
                height: 8px;
                background: url("./../img/icons/arrow-bottom-purple.svg") no-repeat center / contain;
                position: absolute;
                top: 8px;
                right: 0;
            }
        }
    }
}

// media stories
@media (max-width: 1024px) {
    .stories {
        &__top {
            flex-direction: column;
            gap: 20px;
        }
        &__title {
            flex-basis: 100%;
        }
        &__subtitle {
            flex-basis: 100%;
            max-width: none;
        }
    }
}
@media (max-width: 768px) {
    .stories {
        padding: 70px 0;
        &__top {
            gap: 12px;
            margin-bottom: 30px;
        }
        &__card {
            padding-top: 16px;
            &_wrap {
                padding-right: 16px;
                padding-left: 16px;
            }
            &_text {
                font-size: 14px;
            }
            &_name {
                margin-bottom: 20px;
                & p {
                    font-size: 20px;
                }
            }
            &_blocks {
                gap: 4px;
                margin-bottom: 16px;
            }
            &_block {
                padding: 10px 16px;
                margin: 0 -16px;
            }
            &_list {
                margin-bottom: 16px;
                & ul li {
                    padding-left: 14px;
                    &:not(:last-child) {
                        padding-bottom: 14px;
                        margin-bottom: 10px;
                    }
                    &::before {
                        width: 8px;
                        height: 8px;
                    }
                }
            }
        }
    
        &__quote {
            border: 1px solid var(--osnovnoy-tusklyy);
            padding: 16px;
            &_img-marks {
                width: 19px;
                height: 16px;
            }
            &_top {
                margin-bottom: 16px;
            }
            &_text {
                max-height: 56px;
            }
        }
    }
}