
// myths
.myths {
    padding: 150px 0;
    background: var(--fon-blokov);

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__title {
        max-width: 500px;
    }

    &__list {
        max-width: 880px;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        border-radius: 5px;
        padding: 24px;
        box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
        background: var(--fon);

        &_row {
            display: flex;
            align-items: center;
            gap: 16px;

            &.accordion--active {
                padding-bottom: 28px;
                border-bottom: 1px solid var(--seryy);
                margin-bottom: 28px;
                & .myths__item_btn-more svg path {
                    fill: var(--osnovnoy-tusklyy);
                }
            }

            &--true {
                align-items: flex-start;
                margin-bottom: 20px;
            }
        }

        &_tag {
            border-radius: 5px;
            padding: 10px 13px;
            font-weight: 500;
            font-size: 16px;
            line-height: 105%;
            flex-basis: 84px;
            &--false {
                border: 1px solid rgba(205, 127, 132, 0.5);
                color: var(--akcentnyy-krasnyy);
            }
            &--true {
                border: 1px solid rgba(114, 206, 198, 0.5);
                color: var(--akcentnyy-zelenyy);
            }
        }

        &_name {
            flex-grow: 1;
            color: var(--shrift-temnyy);
            text-align: left;
        }

        &_btn-more svg path {
            transition: all 0.3s ease-in;
        }

        &_additions {
            display: flex;
            gap: 8px;
        }

        &_addition {
            flex: 1 0 45%;
            border-radius: 5px;
            padding: 16px 20px 20px 20px;
            &.fact {
                background: rgba(172, 173, 223, 0.08) url("./../img/icons/myths-fact.svg") no-repeat;
                background-position: calc(100% + 10px) calc(100% + 10px);
                color: var(--osnovnoy-tusklyy);
            }
            &.warning {
                background: rgba(205, 127, 132, 0.08) url("./../img/icons/myths-warning.svg") no-repeat;
                background-position: calc(100% + 10px) calc(100% + 10px);
                color: var(--akcentnyy-krasnyy);
            }

            & span {
                display: block;
                font-size: 16px;
                line-height: 120%;
                opacity: 0.4;
                margin-bottom: 6px;
            }

            & p {
                max-width: 85%;
                font-style: italic;
                font-weight: 500;
            }
        }
    }
}

// media myths
@media (max-width: 1024px) {
    .myths {
        padding: 150px 0;
        background: var(--fon-blokov);
        &__row {
            flex-direction: column;
            gap: 60px;
        }
        &__title {
            max-width: none;
        }
        &__list {
            max-width: none;
        }
    }
}

@media (max-width: 768px) {
    .myths {
        padding: 70px 0;
        &__row {
            gap: 30px;
        }
        &__item {
            padding: 16px;
            &_row {
                flex-wrap: wrap;
                justify-content: space-between;
                gap: 12px;
                &.accordion--active {
                    padding-bottom: 20px;
                    margin-bottom: 20px; 
                }
            }
            &_tag {
                padding: 7px 10px;
                font-size: 14px;
                flex-basis: auto;
            }
            &_name {
                order: 2;
                flex-basis: 100%;
            }
            &_additions {
                flex-direction: column;
            }
    
            &_addition {
                flex: 1 0 45%;
                padding: 12px 16px 16px 16px;
                &.fact, &.warning {
                    background-size: 62px 69px;
                    background-position: calc(100% + 20px) calc(100% + 4px);
                }
                & span {
                    font-size: 14px;
                }
            }
        }
    }
}