
// articles-page
.articles-page {
    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 32px 20px;
    }

    &__card {
        display: block;
        padding-bottom: 32px;
        border-bottom: 1px solid var(--seryy);
        &:hover {
            .articles-page__card_title {
                color: var(--osnovnoy-tusklyy);
            }
        }

        &_picture {
            margin-bottom: 20px;
            min-height: 280px;
            & img {
                min-height: 280px;
                max-height: 280px;
            }
        }

        &_date {
            font-size: 16px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--osnovnoy-tusklyy);
            margin-bottom: 12px;
        }

        &_title {
            font-weight: 500;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            transition: all 0.3s ease;
            margin-bottom: 12px;
        }

        &_description {
            font-style: italic;
            color: var(--shrift-tekst-na-fone);
        }
    }
}

// media articles
@media (max-width: 1200px) {
    .articles-page {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 768px) {
    .articles-page {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 28px;
        }
        &__card {
            padding-bottom: 28px;
            &_picture {
                margin-bottom: 20px;
                min-height: 180px;
                & img {
                    min-height: 180px;
                    max-height: 220px;
                }
            }
            &_date {
                font-size: 14px;
                margin-bottom: 8px;
            }
            &_title {
                font-size: 18px;
            }
        }
    }
}
