

// legal-doc

.legal-doc {
    &__title {
        font-weight: 500;
        font-size: 42px;
        line-height: 105%;
        letter-spacing: -0.02em;
        color: var(--shrift-temnyy);
        margin-bottom: 40px;
    }

    &__docs {
        &_row {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 50px;
        }
        &_list ul {
            & li {
                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid var(--seryy);
                    margin-bottom: 16px;
                }

                line-height: 120%;
                color: var(--shrift-temnyy);
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 20px;
                & a {
                    border-radius: 40px;
                    background: var(--osnovnoy-tusklyy);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-width: 44px;
                    width: 44px;
                    height: 44px;
                    transition: all 0.2s ease-in;
                    &:hover {
                        opacity: 0.8;
                    }
                }
            }
        }
    }

    &__licenses {
        &_title {
            margin-bottom: 80px;
        }

        &_cards {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 20px;
        }
        &_card {
            border-radius: 5px;
            padding: 28px;
            background: var(--fon-blokov);
            min-height: 425px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
        }
        &_tag {
            padding: 8px 16px;
            border: 1px solid rgba(172, 173, 223, 0.5);
            border-radius: 5px;
            font-size: 16px;
            color: var(--osnovnoy-tusklyy);
            width: fit-content;
            margin-bottom: 16px;
        }
        &_name {
            font-weight: 500;
            font-size: 22px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
        }
        &_link {
            padding: 24px;
            background: var(--osnovnoy-tusklyy);
            &:hover {
                background: var(--osnovnoy-tusklyy);
                opacity: 0.7;
            }
        }
    }
}

// media legal-doc
@media (max-width: 1280px) {
    .legal-doc {
        &__licenses {
            &_cards {
                grid-template-columns: repeat(3, 1fr);
                gap: 20px;
            }
        }
    }
}
@media (max-width: 1024px) {
    .legal-doc {
        &__docs {
            &_row {
                display: grid;
                grid-template-columns: repeat(1, 1fr);
                gap: 0;
            }
        }
        &__licenses {
            &_cards {
                grid-template-columns: repeat(2, 1fr);
                gap: 12px;
            }
            &_card {
                min-height: 325px;
            }
        }
    }
}
@media (max-width: 768px) {
    .legal-doc {
        &__title {
            font-size: 24px;
            margin-bottom: 30px;
        }

        &__docs {
            &_list ul {
                & li {
                    &:not(:last-child) {
                        padding-bottom: 14px;
                        margin-bottom: 10px;
                    }
                    & a {
                        min-width: 30px;
                        width: 30px;
                        height: 30px;
                    }
                }
            }
        }

        &__licenses {
            &_title {
                margin-bottom: 30px;
            }
            &_cards {
                grid-template-columns: repeat(20, 1fr);
                gap: 8px;
                overflow-x: auto;
                scroll-snap-type: x mandatory;
                scroll-behavior: smooth;
                &::-webkit-scrollbar {
                    width: 0;
                    height: 0;
                }
            }
            &_card {
                padding: 16px;
                min-height: 280px;
                min-width: 280px;
                width: calc(100vw - 60px);
                scroll-snap-align: start;
            }
            &_tag {
                font-size: 14px;
                margin-bottom: 12px;
            }
            &_name {
                font-size: 20px;
            }
            &_link {
                padding: 20px;
            }
        }
    }
}