
// legal-info
.legal-info {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    &__item {
        border-radius: 5px;
        background: var(--fon-blokov);
    }

    & .accordion__btn-more {
        min-width: 10px;
    }

    &__btn {
        padding: 30px 32px;
        font-weight: 500;
        font-size: 24px;
        line-height: 105%;
        text-align: left;
        color: var(--shrift-temnyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        width: 100%;
    }

    &__text {
        padding: 10px 32px 32px;
        & p:not(:last-child) {
            margin-bottom: 22px;
        }

        & ul {
            padding-top: 16px;
            border-top: 1px solid #e6e6eb;
            margin-bottom: 28px;
            & li {
                padding-bottom: 20px;
                border-bottom: 1px solid #e6e6eb;
                margin-bottom: 16px;
                & p, & a {
                    font-weight: 500;
                    line-height: 105%;
                    color: var(--shrift-temnyy);
                }

                & span {
                    display: block;
                    font-style: italic;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 120%;
                    color: var(--shrift-tekst-na-fone);
                    margin-bottom: 8px;
                    position: relative;
                    padding-left: 18px;
                    &::before {
                        content: "";
                        display: block;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star.svg") no-repeat center / contain;
                        position: absolute;
                        top: 4px;
                        left: 0;
                    }
                }
            }
        }

        &_row {
            display: flex;
            gap: 40px;
        }
        &_block {
            flex: 1 0 45%;
            &.green {
                & .legal-info__text_name {
                    color: var(--akcentnyy-zelenyy);
                }
                & ul li span::before {
                    background: url("./../img/icons/star-green.svg") no-repeat center / contain;
                }
            }
        }
        &_name {
            font-weight: 500;
            font-size: 20px;
            line-height: 120%;
            color: var(--osnovnoy-tusklyy);
        }
    }
}

// madia intro
@media (max-width: 1024px) {
    .legal-info {
        &__text {
            &_row {
                flex-direction: column;
                gap: 0;
            }
            & ul {
                padding-top: 0;
                border-top: none;
            }
        }
    }
}

@media (max-width: 768px) {
    .legal-info {
        &__btn {
            padding: 16px;
            font-size: 18px;
        }
    
        &__text {
            padding: 8px 16px 16px;
            & p:not(:last-child) {
                margin-bottom: 18px;
            }
    
            & ul {
                margin-bottom: 18px;
                & li {
                    padding-bottom: 14px;
                    margin-bottom: 10px;
                    & span {
                        font-size: 14px;
                        padding-left: 14px;
                        &::before {
                            width: 8px;
                            height: 8px;
                        }
                    }
                }
            }
            &_name {
                font-size: 16px;
            }
        }
    }
}