
// form
.forms {
    padding: 150px 0;
    background: var(--osnovnoy-tusklyy);
    position: relative;
    overflow: hidden;

    &__row {
        display: flex;
        justify-content: space-between;
    }

    &__bg {
        &_item {
            width: 100%;
            position: absolute;
            left: 0;
            & img {
                width: 100%;
                height: 100%;
                min-height: 101px;
                object-fit: cover;
            }
            &:first-child {
                top: -40px;
            }
            &:last-child {
                bottom: -40px;
            }
        }
    }

    &__text {
        flex: 1 0 50%;
        padding-right: 40px;
    }

    &__tag {
        border: 1px solid rgba(251, 251, 254, 0.5);
        border-radius: 5px;
        padding: 10px 20px;
        color: var(--shrift-svetlyy);
        width: fit-content;
        margin-bottom: 16px;
    }

    &__title {
        color: var(--shrift-svetlyy);
        margin-bottom: 20px;
        & i {
            display: block;
        }
    }

    &__subtitle {
        font-style: italic;
        color: #dfdff2;
        width: 80%;
    }

    &__content {
        flex: 1 0 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 36px;
    }
    
    &__phone {
        display: flex;
        align-items: flex-end;
        gap: 16px;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(251, 251, 254, 0.15);
        & p {
            font-style: italic;
            color: #dfdff2;
        }
        & a {
            font-weight: 500;
            font-size: 24px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: var(--shrift-svetlyy);
            text-wrap: nowrap;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 100%;
                height: 5px;
                background: var(--akcentnyy-zelenyy);
                position: absolute;
                bottom: 4px;
                left: 11px;
            }

            & span {
                position: relative;
                z-index: 2;
            }
        }
    }

    &__form {
        display: flex;
        align-items: flex-end;
        gap: 8px;

        &_input-wrap {
            flex: 1 0 45%;
            & .label {
                color: #dfdff2;
            }
    
            & input::placeholder {
                color: #dfdff2;
            }
        }
        
        &_btn {
            flex: 1 0 45%;
            padding: 22px;
        }

    }
}

// media form
@media (max-width: 1140px) {
    .forms {
        &__row {
            display: flex;
            flex-direction: column;
            gap: 30px;
        }
        &__text {
            padding-right: 0;
        }
    }
}
@media (max-width: 768px) {
    .forms {
        padding: 70px 0;
        &__row {
            gap: 16px;
        }
    
        &__bg {
            &_item {
                & img {
                    min-height: 56px;
                }
                &:first-child {
                    top: -20px;
                }
                &:last-child {
                    bottom: -20px;
                }
            }
        }

        &__tag {
            font-size: 14px;
            padding: 7px 12px;
            margin-bottom: 14px;
        }
        &__title {
            margin-bottom: 12px;
        }
        &__subtitle {
            width: 100%;
        }

        &__phone {
            flex-wrap: wrap;
            gap: 8px;
            padding-bottom: 0;
            border-bottom: none;
            padding-top: 12px;
            border-top: 1px solid rgba(251, 251, 254, 0.15);
            & a {
                font-size: 22px;
            }
        }
        &__form {
            flex-direction: column;
            align-items: stretch;
        }
    }
}