
// linking
.linking {
    &__title {
        margin-bottom: 80px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        margin-bottom: 30px;
    }

    &__item {
        width: calc(100% / 4 - 15px);
        & a {
            border-radius: 5px;
            padding: 16px;
            background: var(--fon-blokov);
            color: var(--shrift-temnyy);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 27px;
            height: 100%;
            &:hover {
                & .linking__item_arrow {
                    background: transparent;
                    & svg path {
                        fill: var(--osnovnoy-tusklyy);
                    }
                }
            }
        }

        &_arrow {
            border-radius: 5px;
            background: var(--osnovnoy-tusklyy);
            border: 1px solid var(--osnovnoy-tusklyy);
            min-width: 48px;
            width: 48px;
            height: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.2s ease-in;
            & svg path {
                transition: all 0.2s ease-in;
            }
        }
    }

    &__sort {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 80px;

        &_wrap {
            max-width: 50%;
            width: 100%;
            & .label {
                color: var(--shrift-tekst-na-fone);
            }
        }
    }

    &__select {
        border: 1px solid rgba(46, 55, 73, 0.3);
        color: var(--shrift-temnyy);
        background: url(./../img/icons/arrow-bottom-black.svg) no-repeat right;
        background-position-x: calc(100% - 30px);
        &:hover {
            border: 1px solid rgba(46, 55, 73, 0.5);
        }
        &:focus {
            border: 1px solid var(--shrift-temnyy);
        }
    }

    &__block {
        &:not(:last-child) {
            padding-bottom: 10px;
            border-bottom: 1px solid var(--seryy);
            margin-bottom: 40px;
        }

        &_title {
            font-weight: 500;
            font-size: 42px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            margin-bottom: 36px;
        }
    }
}

// media linking
@media (max-width: 1200px) {
    .linking {
        &__item {
            width: calc(100% / 3 - 14px);
        }
    }
}

@media (max-width: 1024px) {
    .linking {
        &__item {
            width: calc(100% / 2 - 10px);
        }
    }
}

@media (max-width: 768px) {
    .linking {
        &__title {
            margin-bottom: 30px;
        }
        &__list {
            gap: 8px;
            margin-bottom: 28px;
        }
    
        &__item {
            width: 100%;
            & a {
                padding: 11px 10px;
                gap: 18px;
            }
    
            &_arrow {
                min-width: 32px;
                width: 32px;
                height: 32px;
            }
        }


        &__sort {
            margin-bottom: 30px;
            &_wrap {
                max-width: 100%;
            }
        }
        &__select {
            background-position-x: calc(100% - 16px);
        }
        &__quantity {
            display: none;
        }
        &__block {
            &:not(:last-child) {
                padding-bottom: 0;
                margin-bottom: 24px;
            }
            &_title {
                font-size: 24px;
                margin-bottom: 20px;
            }
        }
    }
}