
// rooms
.rooms {
    padding: 150px 0;
    background: var(--fon-blokov);
    overflow: hidden;

    &__top {
        display: flex;
        gap: 40px;
        margin-bottom: 80px;
    }
    &__title {
        flex-basis: 50%;
    }
    &__subtitle {
        flex-basis: 50%;
        max-width: 650px;
        font-style: italic;
    }

    &__tab-btns {
        border: 1px solid var(--osnovnoy-tusklyy);
        border-radius: 5px;
        padding: 6px;
        display: flex;
        gap: 20px;
        margin-bottom: 30px;
    }

    &__tab-btn {
        padding: 14.5px;
        flex: 1 0 30%;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
        transition: all 0.2s ease-in;
        & svg path {
            transition: all 0.2s ease-in;
        }
        &:hover {
            background: rgba(172,173,223, 0.16);
        }
        &.active {
            background: var(--osnovnoy-tusklyy);
            color: var(--shrift-svetlyy);
            & svg path {
                fill: var(--fon);
            }
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        gap: 10px;
        overflow: hidden;

        &_photo {
            position: relative;
        }
        
        &_picture img {
            max-height: 240px;
            min-height: 220px;
        }

        &_recomend {
            border-radius: 0 5px;
            padding: 10px 14px;
            background: var(--akcentnyy-zheltyy);
            font-size: 16px;
            color: var(--shrift-temnyy);
            display: flex;
            align-items: center;
            gap: 10px;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 3;
            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                background: url("./../img/icons/star-black.svg") no-repeat center / contain;
            }
        }

        &_content {
            border-radius: 5px;
            padding: 24px;
            box-shadow: 0 2px 10px 0 rgba(46, 55, 73, 0.04);
            background: var(--fon);
        }

        &_name {
            font-weight: 500;
            font-size: 28px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            margin-bottom: 10px;
        }

        &_info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 32px;
        }

        &_seat {
            font-size: 16px;
            font-style: italic;
        }

        &_cost {
            font-size: 16px;
            line-height: 105%;
            letter-spacing: -0.02em;
            color: var(--shrift-temnyy);
            position: relative;
            & i {
                font-style: italic;
                font-weight: 500;
                font-size: 20px;
                text-wrap: nowrap;
            }
            & span {
                position: relative;
                z-index: 2;
            }
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 4px;
                background: var(--akcentnyy-zelenyy);
                position: absolute;
                bottom: 2px;
                left: 0;
            }
        }

        &_list {
            & ul {
                display: flex;
                flex-direction: column;
                gap: 12px;
                margin-bottom: 20px;
                & li {
                    padding-bottom: 16px;
                    border-bottom: 1px solid var(--seryy);
                    padding-left: 18px;
                    position: relative;
                    color: var(--shrift-temnyy);
                    &:first-child {
                        padding-top: 12px;
                        border-top: 1px solid var(--seryy);
                        &::before {
                            top: 20px;
                        }
                    }
                    &::before {
                        content: "";
                        display: block;
                        width: 10px;
                        height: 10px;
                        background: url("./../img/icons/star.svg") no-repeat center / contain;
                        position: absolute;
                        top: 4px;
                        left: 0;
                    }
                }
            }
        }
    }
}

// media rooms
@media (max-width: 768px) {
    .rooms {
        padding: 70px 0;
        &__top {
            flex-direction: column;
            gap: 12px;
            margin-bottom: 30px;
        }
        &__title {
            flex-basis: 100%;
        }
        &__subtitle {
            flex-basis: 100%;
            max-width: none;
        }
    
        &__tab-btns {
            padding: 4px;
            gap: 4px;
            margin-bottom: 16px;
        }
        &__tab-btn {
            padding: 9px;
            gap: 6px;
        }
    
        &__card {
            gap: 6px;
            &_picture img {
                max-height: 220px;
                min-height: 180px;
            }
            &_recomend {
                padding: 6px 10px;
                font-size: 14px;
                gap: 8px;
            }
            &_content {
                padding: 16px;
            }
            &_name {
                font-size: 22px;
                margin-bottom: 8px;
            }
            &_info {
                flex-direction: column;
                align-items: start;
                gap: 16px;
                margin-bottom: 24px;
            }
            &_seat {
                font-size: 14px;
            }
            &_list {
                & ul {
                    gap: 10px;
                    margin-bottom: 24px;
                    & li {
                        padding-bottom: 14px;
                        padding-left: 14px;
                        &:first-child {
                            padding-top: 10px;
                            &::before {
                                top: 16px;
                            }
                        }
                        &::before {
                            width: 8px;
                            height: 8px;
                        }
                    }
                }
            }
        }
    }
}